import React, { useState, useEffect } from "react";
import {Link, useHistory} from "react-router-dom";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";

import grayRectImage from "../../resources/images/gray_rect.png";
import DeletePopup from "../utils/deletePopup";
import Pagination from "../partials/Pagination";
import DownloadPopup from "../utils/dbDownloadPopup";


function ProductList() {
    const [productList, setProductList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [postsPerPage, setPostsPerPage] = useState(10);

    const [deleteModal, setDeleteModal] = useState(false);
    const [downloadModal, setDownloadModal] = useState(false);
    const [overlay, setOverlay] = useState("none");

    const [checkItems, setCheckItems] = useState([]);
    const [checked, setChecked] = useState(false);
    const [idxList, setIdxList] = useState([]);

    useEffect(() => {
        if(!localStorage.getItem("token")) {
            alert('로그인 후 이용해주세요.');
            history.push("/");
        }

        const fetchData = async () => {
            const result = await fetch("/api/products", {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());



            if(result.success) {
                setProductList(result.data);
            }else {
                if(result.code === -1) {
                    alert(result.msg);
                    history.push("/payment");
                }
            }
            
        }

        fetchData();
    }, []);

    let indexOfLast = currentPage * postsPerPage;
    let indexOfFirst = indexOfLast - postsPerPage;
    const currentPosts = productList.slice(indexOfFirst, indexOfLast);


    const goToUpdatePage = () => {
        if(checkItems.length > 1) {
            alert('수정은 한개씩 가능합니다.');
            return;
        }else if(checkItems.length === 0) {
            alert('수정할 상품을 체크해주세요');
            return;
        }

        history.push(`/productUpdate?product_idx=${checkItems[0]}`);

    }

    const handleSingleCheck = (checked, id) => {
        if (checked) {
          setCheckItems([...checkItems, id]);
        } else {
          // 체크 해제
          setCheckItems(checkItems.filter((el) => el !== id));
        }

        console.log(checkItems);
      };
    

    const handleAllCheck = (checked) => {
        if (checked) {
          const idArray = [];
          // 전체 체크 박스가 체크 되면 id를 가진 모든 elements를 배열에 넣어주어서,
          // 전체 체크 박스 체크
          currentPosts.forEach((el) => idArray.push(el.product_idx));
          setCheckItems(idArray);
        }
    
        // 반대의 경우 전체 체크 박스 체크 삭제
        else {
          setCheckItems([]);
        }
    };


    
    const history = useHistory();



    const openDeleteModal = () => {
        setDeleteModal(true);
        setOverlay("block");
    }

    const closeDeleteModal = () => {
        setDeleteModal(false);
        setOverlay("none");
    }

    const openDownloadModal = () => {
        setDownloadModal(true);
        setOverlay("block");
    }

    const closeDownloadModal = () => {
        setDownloadModal(false);
        setOverlay("none");
    }

    const removeProduct = async () => {

        const result = await fetch(`/api/products?_method=DELETE`, {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({
                idxArray: JSON.stringify(checkItems)
            })
        }).then(res => res.json());

        if(result.success) {
            alert('상품이 삭제되었습니다.');
            window.location.reload();
        }
    }

    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];

    const ProductTable = () => {
        const items = [];

        if(productList) {
            for(const [i, data] of currentPosts.entries()) {
                items.push(
                    <tr key={i}>
                        <td>
                            <input type="checkbox" value={data.product_idx} onChange={(e) => handleSingleCheck(e.target.checked, data.product_idx)} checked={checkItems.includes(data.product_idx) ? true: false} />
                        </td>
                        <td>
                            <img src={data.main_image ? data.main_image : grayRectImage} style={{width:'50px', height: '50px'}} />
                        </td>

                        <td>{data.product_name}</td>
                        <td>{data.av_count}개</td>
                        <td>{data.purchase_price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} 원</td>
                        <td>{data.remark}</td>
                        <td>{data.published_date.split("T")[0]}</td>
                        <td>{data.category}</td>
                        <td>비고</td>
                    </tr>
                )
            }
        }

        return(
            <table className="productList">
                <thead>
                    <tr>
                        <th>
                            <input type="checkbox" onChange={(e) => handleAllCheck(e.target.checked)} checked={checkItems.length === currentPosts.length ? true: false}/>
                        </th>
                        <th>상품</th>
                        <th>상품명</th>
                        <th>재고</th>
                        <th>구매가</th>
                        <th>비고</th>
                        <th>등록일</th>
                        <th>플랫폼</th>
                        <th>비고</th>
                    </tr>
                </thead>

                <tbody>
                    {items}
                </tbody>
            </table>
        )
    }

    return(
        <div className="container">
            <div className="overlay" style={{display: overlay}}></div>
            <Header openDeleteModal={openDeleteModal} closeDeleteModal={closeDeleteModal}></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="productList" sidebarLinks={sidebarLinks}></Sidebar>
                <div className="product__wrapper--create">
                    <div className="lists">
                        <h1 className="lists__title">상품 리스트</h1>

                        <ProductTable />

                        <div className="lists__buttonBox">
                            {/* <button className={classnames("lists__processButton", "lists__dbDownloadButton")} onClick={openDownloadModal}>DB 다운로드</button> */}
                            <button className={classnames("lists__processButton", "lists__deleteButton")} onClick={openDeleteModal}>삭제</button>
                            <button className={classnames("lists__processButton", "lists__updateButton")} onClick={goToUpdatePage}>수정</button>
                            <Link to="/productCreate">
                                <button className={classnames("lists__processButton", "lists__createButton")}>등록</button>
                            </Link>

                        </div>
                        <Pagination pdTable="154px" totalPosts={productList.length} paginate={setCurrentPage} postsPerPage={postsPerPage} currentPage={currentPage} ></Pagination>

                    </div>
                </div>
                <DeletePopup isOpen={deleteModal} close={closeDeleteModal} remove={removeProduct}></DeletePopup>
                <DownloadPopup isOpen={downloadModal} close={closeDownloadModal}></DownloadPopup>

            </div>
        </div>
    )
}

export default ProductList;