import React, {useState} from "react";
import classnames from "classnames";
import { Link } from "react-router-dom";

function BoardTable(props) {
    const {boardData} = props;
    const items = [];

    console.log(boardData);
    if(boardData) {

        for(const [i, data] of boardData.entries()) {
            items.push(
                <tr className="boardTable__body" key={i}>
                    <td>{i+1}</td>
                        <td>
                            <Link to={"/board/view?board_type="+ data.board_type + "&post_idx=" + data.post_idx}>
                                {data.title}
                            </Link>
                        </td>
                    <td>{data.author}</td>
                    <td>{data.published_date.split("T")[0]}</td>
                    <td>{data.views_count}</td>
                </tr>
            )
        }
    }
    return(
        <div className="boardTable">
            <table className="board__wrapper">
                <thead>
                    <tr className="boardTable__head">
                        <th>번호</th>
                        <th>제목</th>
                        <th>글쓴이</th>
                        <th>등록일</th>
                        <th>조회</th>
                    </tr>
                </thead>

                <tbody>
                    {items}
                </tbody>
            </table>
        </div>
    )
}

export default BoardTable;