import React, { useState } from "react";
import classnames from "classnames";


function downloadPopup (props) {
    const { isOpen, close, download } = props;
    return(
        <>
        {isOpen ? (
                <div className="modal" style={{width: "508px", height: "580px"}}>
                <p className="modal__title"  style={{marginBottom: "20px"}}>DB다운로드 다이얼로그 변경</p>
                <table className="db__dialog">
                    <colgroup>
                        <col style={{width: "10%"}} />
                        <col style={{width: "10%"}} />
                        <col style={{width: "10%"}} />
                    </colgroup>
                    <thead>
                        <tr className="db__dialog--head">
                            <th><input type="checkbox"/></th>
                            <th>구분</th>
                            <th>수수료</th>
                        </tr>
                    </thead>


                    <tbody>
                        <tr className="db__dialog--content">
                            <td ><input type="checkbox"/></td>
                            <td className="dialog__column">
                                <img src="https://cdn.zeplin.io/60178c940d49cc2042138b83/assets/C36B1FA3-7A83-463B-8156-248B4B5AC846.png" style={{width: "50px", height: "50px", marginRight: "15px"}}/>
                                쿠팡
                            </td>

                            <td>
                                <input type="text"/><span className="won">원</span>
                            </td>
                        </tr>

                        <tr className="db__dialog--content">
                            <td ><input type="checkbox"/></td>
                            <td className="dialog__column">
                                <img src="https://cdn.zeplin.io/60178c940d49cc2042138b83/assets/D2905B6B-CE78-41B1-8F51-38A1D4641E61.png" style={{width: "50px", height: "50px", marginRight: "15px"}}/>
                                11번가
                            </td>

                            <td>
                                <input type="text"/><span className="won">원</span>
                            </td>
                        </tr>

                        <tr className="db__dialog--content">
                            <td ><input type="checkbox"/></td>
                            <td className="dialog__column">
                                <img src="https://cdn.zeplin.io/60178c940d49cc2042138b83/assets/4E40E517-AAB1-4E08-8378-3D2451B7A262.png" style={{width: "50px", height: "50px", marginRight: "15px"}}/>
                                G마켓
                            </td>

                            <td>
                                <input type="text"/><span className="won">원</span>
                            </td>
                        </tr>

                        <tr className="db__dialog--content">
                            <td ><input type="checkbox"/></td>
                            <td className="dialog__column">
                                <img src="https://cdn.zeplin.io/60178c940d49cc2042138b83/assets/BBC44D77-82E8-47AB-AB4F-A4E4390CEC1D.png" style={{width: "50px", height: "50px", marginRight: "15px"}}/>
                                옥션
                            </td>

                            <td>
                                <input type="text"/><span className="won">원</span>
                            </td>
                        </tr>

                        <tr className="db__dialog--content">
                            <td ><input type="checkbox"/></td>
                            <td className="dialog__column">
                                <img src="https://cdn.zeplin.io/60178c940d49cc2042138b83/assets/F9D0F90C-5C74-4752-8681-AA639EE39FA4.png" style={{width: "50px", height: "50px", marginRight: "15px"}}/>
                                네이버
                            </td>

                            <td>
                                <input type="text"/><span className="won">원</span>
                            </td>
                        </tr>                                                                                                                                           
                    </tbody>
                </table>
                <div className="modal__buttonBox">
                    <button className="modal__login--cancel" onClick={close}>취소</button>
                    <button className="modal__remove--btn" onClick={download}>다운로드</button>
                </div>
            </div>
        ): null}
        </>
    )
}

export default downloadPopup;