import React, { useState, useEffect } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardList from "../partials/BoardList";

function NoticeList() {
    const [noticeList, setNoticeList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [postsPerPage, setPostsPerPage] = useState(10);
    useEffect(async () => {
        const noticeData = await fetch("/api/board/notice", {
            method: "GET"
        }).then(result => result.json());
        setNoticeList(noticeData.noticeList);
    }, []);

    
    let indexOfLast = currentPage * postsPerPage;
    let indexOfFirst = indexOfLast - postsPerPage;
    const currentPosts = noticeList.slice(indexOfFirst, indexOfLast);


    const navList = ["공지사항", "문의/답변", "시스템 오류", "시스템 개선 요청"];
    const navURL = ["/board/notice", "/board/question", "/board/issue", "/board/request"];
    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];
    return (
        <div className="container">
            <Header type="boardList"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="boards" sidebarLinks={sidebarLinks}></Sidebar>
                <BoardList selectedNav="공지사항" navList={navList} navURL={navURL} postsPerPage={postsPerPage} boardData={currentPosts} paginate={setCurrentPage} totalPosts={noticeList.length} currentPage={currentPage}></BoardList>
            </div>
        </div>
    )
}

export default NoticeList;