import React, { useEffect, useState } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import AddDefaultImage from "../../resources/images/admin_image_default.png";
import UploadPopup from "../utils/uploadImagePopup";
import { useHistory } from "react-router";

function AdminAds() {
    const [overlay, setOverlay] = useState("none");
    const [imageModal, setImageModal] = useState(false);
    const [imageModal2, setImageModal2] = useState(false);
    const [imageModal3, setImageModal3] = useState(false);
    const [imageModal4, setImageModal4] = useState(false);

    const [thumbnail, setThumbnail] = useState(`url(${AddDefaultImage})`);
    const [thumbnail2, setThumbnail2] = useState(`url(${AddDefaultImage})`);
    const [thumbnail3, setThumbnail3] = useState(`url(${AddDefaultImage})`);
    const [thumbnail4, setThumbnail4] = useState(`url(${AddDefaultImage})`);

    const [thumbnailURL, setThumbnailURL] = useState('');
    const [thumbnailURL2, setThumbnailURL2] = useState('');
    const [thumbnailURL3, setThumbnailURL3] = useState('');
    const [thumbnailURL4, setThumbnailURL4] = useState('');
    const [idx, setIdx] = useState();
    const [ads, setAds] = useState();

    const history = useHistory();

    useEffect(async () => {

        if(!localStorage.getItem("token")) {
            alert("로그인을 해주세요");
            history.push("/");
        }else {

        }
        const images = await fetch("/api/ads/all", {
            method: "GET",
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`
            }
          }).then(res => res.json());

          if(images.success) {
            setThumbnail(`url(${images.images[0].image_url})`);
            setThumbnail2(`url(${images.images[1].image_url})`);
            setThumbnail3(`url(${images.images[2].image_url})`);
            setThumbnail4(`url(${images.images[3].image_url})`);
  
            setAds(images.images);
          }else {
              alert('권한이 없습니다.');
              history.push("/manage");
          }
      
          

    
    }, [])

    const openImageModal = (e) => {
        setImageModal(true);
        setOverlay("block");
        setIdx(1);
    }
    
    const closeImageModal = () => {
        setImageModal(false);
        setOverlay("none");
    }

    const openImageModal2 = (e) => {
        setImageModal2(true);
        setOverlay("block");
        setIdx(2);
    }
    
    const closeImageModal2 = () => {
        setImageModal2(false);
        setOverlay("none");
    }

    
    const openImageModal3 = (e) => {
        setImageModal3(true);
        setOverlay("block");
        setIdx(3);
    }
    
    const closeImageModal3 = () => {
        setImageModal3(false);
        setOverlay("none");
    }

    const openImageModal4 = (e) => {
        setImageModal4(true);
        setOverlay("block");
        setIdx(4);
    }
    
    const closeImageModal4 = () => {
        setImageModal4(false);
        setOverlay("none");
    }



    const thumbnailLinkChange = (e) => {
        setThumbnailURL(e.target.value);
    }

    const thumbnailLinkChange2 = (e) => {
        setThumbnailURL2(e.target.value);
    }

    const thumbnailLinkChange3 = (e) => {
        setThumbnailURL3(e.target.value);
    }

    const thumbnailLinkChange4 = (e) => {
        setThumbnailURL4(e.target.value);
    }


    const getLinkImage = (e) => {
        setThumbnail(`url(${thumbnailURL})`);
    }

    const getLinkImage2 = (e) => {
        setThumbnail2(`url(${thumbnailURL2})`);
    }

    const getLinkImage3 = (e) => {
        setThumbnail3(`url(${thumbnailURL3})`);
    }

    const getLinkImage4 = (e) => {
        setThumbnail4(`url(${thumbnailURL4})`);
    }

    const onClickUpload = () => {
        switch(idx) {
            case 1: {
                uploadAds(1, thumbnailURL)
                closeImageModal();
                break;
            }
            case 2: {
                uploadAds(2, thumbnailURL2)
                closeImageModal2();
                break;
            }
            case 3: {
                uploadAds(3, thumbnailURL3)
                closeImageModal3();
                break;
            }
            case 4: {
                uploadAds(4, thumbnailURL4)
                closeImageModal4();
                break;
            }
        }
    }

    const uploadAds = async (idx, url) => {
        const result = await fetch("/api/ads", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({image_idx: idx, image_path: url})
        }).then(res => res.json());

        if(result.success) {
            alert('등록 되었습니다.');
        }else {
            alert('오류가 발생하였습니다.');
        }
    }


    const sidebarList = ["관리페이지", "게시판", "메인 사진 등록", "결제 이력"];
    const sidebarLinks = ["/admin", "/board/notice", "/admin/ads", "/admin/paymentHistory"];
    return (
        <div className="container">
            <div className="overlay" style={{display: overlay}}></div>
            <Header type="adminAds"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} sidebarLinks={sidebarLinks} selectedItem="templates"></Sidebar>
                <div className="ads__wrapper">
                    <div className="ads__image__boxes">
                        <div className={classnames("ads__image__one", "ads__image")} value="thumbnail1" style={{backgroundImage: thumbnail }} onClick={openImageModal}/>
                        <div className={classnames("ads__image__two", "ads__image")} value="thumbnail2"  style={{backgroundImage: thumbnail2 }} onClick={openImageModal2}/>
                    </div>

                    <div className="ads__image__boxes">
                        <div className={classnames("ads__image__three", "ads__image")} value="thumbnail3"  style={{backgroundImage: thumbnail3 }} onClick={openImageModal3}/>
                        <div className={classnames("ads__image__four", "ads__image")} value="thumbnail4"  style={{backgroundImage: thumbnail4 }} onClick={openImageModal4} />
                    </div>

{/*                     
                    <div className="ads__btn__wrapper">
                        <div className="ads__btn--save">저장</div>
                    </div> */}
                </div>
            </div>

            <UploadPopup isOpen={imageModal} close={closeImageModal} changeInputPopup={thumbnailLinkChange} thumbnail={thumbnail} getLink={getLinkImage} click={onClickUpload}></UploadPopup>
            <UploadPopup isOpen={imageModal2} close={closeImageModal2} changeInputPopup={thumbnailLinkChange2} thumbnail={thumbnail2} getLink={getLinkImage2} click={onClickUpload}></UploadPopup>
            <UploadPopup isOpen={imageModal3} close={closeImageModal3} changeInputPopup={thumbnailLinkChange3} thumbnail={thumbnail3} getLink={getLinkImage3} click={onClickUpload}></UploadPopup>
            <UploadPopup isOpen={imageModal4} close={closeImageModal4} changeInputPopup={thumbnailLinkChange4} thumbnail={thumbnail4} getLink={getLinkImage4} click={onClickUpload}></UploadPopup>
        </div>
    )
}

export default AdminAds;