import React, { useEffect, useState } from "react";
import classnames from "classnames";
import Sidebar from "../partials/Sidebar";
import Header from "../partials/Header";
import sampleGraphImage from "../../resources/images/sample_graph.png";
import sampleMember1 from "../../resources/images/sample_profile1.png";
import sampleMember2 from "../../resources/images/sample_profile2.png";
import sampleMember3 from "../../resources/images/sample_profile3.png";
import sampleMember4 from "../../resources/images/sample_profile4.png";
import { useHistory } from "react-router";
function AdminMain() {


    const [adminList, setAdminList] = useState();
    const [totalAmount, setTotalAmount] = useState();
    const [userCount, setUserCount] = useState();
    const [todayVisitor, setTodayVisitor] = useState();
    const adminItems = [];
    const history = useHistory();
    useEffect(async () => {

        if(!localStorage.getItem("token")) {
            alert("로그인을 해주세요");
            history.push("/");
        }else {
            const result = await fetch("/api/admin/manage", {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());

            if(result.success) {
                console.log(result);
                setAdminList(result.adminList);
                setTotalAmount(result.totalAmount);
                setUserCount(result.userCount);
                setTodayVisitor(result.todayVisitor);


            }else {
                alert('권한이 없습니다.');
                history.push("/manage");
            }


        }
    }, []);

    if(adminList) {

        for(let i=0; i<adminList.length; i++) {
            adminItems.push(
                <div className="admin__members--profile">
                    <img src={sampleMember2} />
                    <p className="admin__member--nickname">{adminList[i].name}</p>
                    <p className="admin__member--email">{adminList[i].email}</p>
                </div>    
            )
        }
    }
    const sidebarList = ["관리페이지", "게시판", "메인 사진 등록", "결제 이력"];
    const sidebarLinks = ["/admin", "/board/notice", "/admin/ads", "/admin/paymentHistory"];
    return(
        <div className="container">
            <Header></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="manage" sidebarLinks={sidebarLinks}></Sidebar>
                <div className="admin__main__wrapper">
                    <div className="admin__manage--boxes">
                        <div className="admin__manage__box">
                            <p className="admin__manage__subtitle">총 회원수</p>
                            <h3 className="admin__manage__count">{userCount ? userCount : null}</h3>
                        </div>

                        <div className="admin__manage__box">
                            <p className="admin__manage__subtitle">1일 접속자 수</p>
                            <h3 className="admin__manage__count">{todayVisitor}</h3>
                        </div>
                    </div>

                    <div className="admin__manage--boxes">
                        <div className="admin__manage__box">
                            <p className="admin__manage__subtitle">결제 금액</p>
                            <h3 className="admin__manage__count">{totalAmount ? totalAmount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : null} ₩</h3>
                        </div>

                        <div className={classnames("admin__manage__box", "no-bgImg")}>
                            <p className="admin__manage__subtitle">관리자 멤버</p>
                            <div className="admin__members">
                                
                                {
                                    adminItems ? adminItems : null
                                }                        
                            
                            </div>
                        </div>
                    </div>                    
                 
                </div>
            </div>
        </div>
    )
}

export default AdminMain;
