import React, {useState, useEffect} from "react";
import { useHistory } from "react-router";
import DeletePopup from "../utils/deletePopup";

function TemplateChild (props)  {
    const {click, order, data, key} = props;
    return(
        <>
            <div className="template__lists--list" onClick={click} order={order} data={JSON.stringify(data)} >
                {data ? data.template_name : null}
            </div>
        </>
    )
}

function ReturnTemplate() {
    const [overlay, setOverlay] = useState("none");
    const [deleteModal, setDeleteModal] = useState(false);

    const [template, setTemplate] = useState([]);
    const [selecetedTemplate, selectTemplate] = useState();

    const history = useHistory();
    const [selectedIdx, selectIdx] = useState();

    const [templateName, setTemplateName] = useState();
    const [asNumber, setAsNumber] = useState();
    const [asInfo, setAsInfo] = useState();
    const [sellerInfo, setSellerInfo] = useState();


    useEffect(() => {
        if(!localStorage.getItem("token")) {
            alert('로그인 후 이용해주세요.');
            history.push("/");
        }
        const fetchData = async () => {
            const result = await fetch("/api/template/as/all", {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());
    

            if(result.success) {
                const tempArray = [];
                for(let i=0; i<result.data.length; i++) {
                    tempArray.push(
                        <TemplateChild click={onClickTemplate} data={result.data[i]} order={i} key={i}/>
                    )
                }
                
    
                setTemplate(tempArray);
            }else {
                if(result.code === -1) {
                    alert(result.msg);
                    history.push("/payment");
                }
            }
        }

        fetchData();

    }, [])


    const onClickTemplate = (e) => {
        selectTemplate(e.currentTarget);
        const customJson = JSON.parse(e.currentTarget.getAttribute("data"));
        selectIdx(customJson.template_idx);
        setTemplateName(customJson.template_name);
        setAsNumber(customJson.as_number);
        setAsInfo(customJson.as_info);
        setSellerInfo(customJson.seller_info);
        /* TODO Change color */
    }

    const onClickAddTemplate = () => {
        setTemplate([...template, <TemplateChild click={onClickTemplate}/>])
    }

    
    const openDeleteModal = () => {
        setDeleteModal(true);
        setOverlay("block");
    }
    
    const closeDeleteModal = () => {
        setDeleteModal(false);
        setOverlay("none");
    }
    
    const removeProduct = async () => {
        if(!selectedIdx) {
            alert('삭제할 템플릿을 선택해주세요.');
            return;
        }

        const result = await fetch("/api/template/as?_method=DELETE", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({selectedIdx})
        }).then(res => res.json());

        if(result.success) {
            alert('삭제 되었습니다.');
            closeDeleteModal();
            window.location.reload();
        }
    }

    const onChangeTemplateName = (e) => {
        setTemplateName(e.target.value);
    }

    const onChangeAsNumber = (e) => {
        setAsNumber(e.target.value);
    }

    const onChangeAsInfo = (e) => {
        setAsInfo(e.target.value);
    }

    const onChangeSellerInfo = (e) => {
        setSellerInfo(e.target.value);
    }

    const onCreateAs = async () => {
        let customJson = {
            templateName: templateName,
            asNumber: asNumber,
            asInfo: asInfo,
            sellerInfo: sellerInfo
        };

        console.log(customJson);

        const result = await fetch("/api/template/as", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({requestJson: customJson, selectedIdx})
        }).then(res => res.json());

        if(result.success) {
            alert('템플릿이 등록되었습니다.');
            window.location.reload();
        }else {
            alert('오류가 발생하였습니다.');
        }
    }

    return(
        <div className="template__wrapper">
            <div className="overlay" style={{display: overlay}}></div>
            <h1 className="template__title">A/S특이사항 템플릿 목록</h1>
            <div className="template__boxes">
                <div className="template__lists">
                    {template}


                    <div className="template__lists--addBtn" onClick={onClickAddTemplate}>템플릿 추가</div>
                </div>

                <div className="template__contents">
                    <div className="template__contents__row">
                        <div className="template__contents__row--title">이름</div>
                        <input type="text" className="template__contents__row--input" value={templateName} onChange={onChangeTemplateName} />
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">A/S 전화번호</div>
                        <input type="text" className="template__contents__row--input" value={asNumber} onChange={onChangeAsNumber} />
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">A/S 안내</div>
                        <textarea className="template__contents__row--textarea" rows="10" value={asInfo} onChange={onChangeAsInfo}/>
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">판매자 특이사항</div>
                        <textarea className="template__contents__row--textarea" rows="10" value={sellerInfo} onChange={onChangeSellerInfo}/>
                    </div>
                                                                               
                </div>
            </div>

            <div className="template__action__box">
                    <div className="template__deleteBtn" onClick={openDeleteModal}>
                        삭제
                    </div>
                    <div className="template__createBtn" onClick={onCreateAs}>
                        등록/수정
                    </div>
            </div>
            <DeletePopup isOpen={deleteModal} close={closeDeleteModal} remove={removeProduct}></DeletePopup>
        </div>
    )
}

export default ReturnTemplate;