import React, { useState } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardNav from "../partials/BoardNav";

import DeliveryTemplate from "../partials/deliveryTemplate";

function Delivery() {
    const navList = ["배송", "반품/교환", "A/S특이사항", "상세 / 상하단 이미지"];
    const navURL = ["/template/delivery", "/template/return", "/template/as", "/template/images"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];
    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    return (
        <div className="container">
            <Header type="template"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="templates" sidebarLinks={sidebarLinks}></Sidebar>
                
                <BoardNav selectedNav="배송" navList={navList} navURL={navURL}>
                    <DeliveryTemplate></DeliveryTemplate>
                </BoardNav>
            </div>
        </div>
    )
}

export default Delivery;