import React, {useState} from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import { useHistory } from "react-router";


function Payment() {
    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];

    const {IMP} = window;

    const history = useHistory();
    const payments = async (month) => {
        const result = await fetch("/api/payment/start", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({month: month})
        }).then(res => res.json());

        console.log(result);

        if(result.success && result.amount) {
            IMP.request_pay({ // param
                pg: "html5_inicis",
                pay_method: "card",
                merchant_uid: `${result.merchantUID}`,
                name: `${month}개월 결제`,
                amount: result.amount,
                buyer_email: `${result.email}`,
                buyer_name: `${result.name}`
            }, async rsp => { // callback
                if (rsp.success) {
                    
                    const finalResult = await fetch("/api/payment/complete?_method=PUT", {
                        headers: {
                            Authorization: `Bearer ${localStorage.getItem("token")}`,
                            "Content-Type": "application/json"
                        },
                        method: "POST",
                        body: JSON.stringify({
                            imp_uid: rsp.imp_uid
                        })
                    }).then(res => res.json());

                    console.log(rsp);
                    
                    if(finalResult.success) {
                        alert("결제 성공하였습니다.");
                        history.push("/manage");
                    }else {
                        alert(finalResult.msg);
                    }
                    // 결제 성공 시 로직,
    
                } else {
                    alert('결제가 완료되지 않았습니다. 재시도 해주세요.');
    
                }
            });
        }else {
            alert('잘못된 요청입니다.');
        }
    }
    return(
        <div className="container">
            <Header></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="" sidebarLinks={sidebarLinks}></Sidebar>
                <div className="product__wrapper--create">
                    <div className="payment">
                        <div className="payment__title--box">
                            <h1 className="payment__title">Item Booster</h1>
                            <h1 className="payment__title">상품 가격 안내</h1>
                        </div>

                        <div className="payment__main--box">
                            <p className="payment__main--alert">만료 9일 전</p>
                            <div className="payment__sub__boxes">
                                <div className="payment__sub--box">
                                    <div className="payment__sub--monthly">1개월 결제</div>
                                    
                                    <div className="payment__sub--money">
                                        100,000<span className="won">원</span>
                                    </div>

                                    <div className={classnames("payment__sub--dc", "visNoneWhite")}>
                                        (-5%)
                                    </div>
                                    
                                    <div className="payment__sub--contents">
                                        <p className="payment__contents--text">상품 설명</p>
                                        <p className="payment__contents--text">상품 설명</p>
                                        <p className="payment__contents--text">상품 설명</p>
                                    </div>
                                    
                                    <div className="payment__sub__footer">
                                        <button className="payButton" onClick={() => {payments(1)}}>결제하기</button>
                                    </div>
                                </div>

                                
                                <div className="payment__sub--box">
                                    <div className="payment__sub--monthly">3개월 결제</div>
                                    
                                    <div className="payment__sub--money">
                                        285,000<span className="won">원</span>
                                    </div>

                                    <div className="payment__sub--dc">
                                        (-5%)
                                    </div>

                                    
                                    
                                    <div className="payment__sub--contents">
                                        <p className="payment__contents--text">상품 설명</p>
                                        <p className="payment__contents--text">상품 설명</p>
                                        <p className="payment__contents--text">상품 설명</p>
                                    </div>
                                    
                                    <div className="payment__sub__footer">
                                        <button className="payButton" onClick={() => {payments(3)}}>결제하기</button>
                                    </div>
                                </div>

                                
                                <div className={classnames("payment__sub--box", "payment__sub__box--blue")}>
                                    <div className={classnames("payment__sub--monthly", "payment__sub__monthly--blue")}>6개월 결제</div>
                                    
                                    <div className="payment__sub--money">
                                        560,000<span className="won">원</span>
                                    </div>

                                    <div className="payment__sub--dc">
                                        (-10%)
                                    </div>
                                    
                                    <div className="payment__sub--contents">
                                        <p className="payment__contents--text">상품 설명</p>
                                        <p className="payment__contents--text">상품 설명</p>
                                        <p className="payment__contents--text">상품 설명</p>
                                    </div>
                                    
                                    <div className="payment__sub__footer">
                                        <button onClick={() => {payments(6)}} className={classnames("payButton", "payButton--blue")}>결제하기</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Payment;