import React, {useState, useEffect} from "react";
import { useHistory } from "react-router";
import DeletePopup from "../utils/deletePopup";
function TemplateChild (props)  {
    const {click, data, order} = props;
    return(
        <>
            <div className="template__lists--list" onClick={click} order={order} data={JSON.stringify(data)} >
                {data ? data.template_name : null}
            </div>
        </>
    )
}

function ReturnTemplate() {

    const history = useHistory();
    const [template, setTemplate] = useState([]);
    const [selecetedTemplate, selectTemplate] = useState();
    const [overlay, setOverlay] = useState("none");
    const [deleteModal, setDeleteModal] = useState(false);

    const [selectedIdx, selectIdx] = useState();
    const [templateName, setTemplateName ] = useState();
    const [returnDelivery, setReturnDelivery] = useState();
    const [returnCostSingle, setReturnCostSingle] = useState();
    const [returnCostRound, setReturnCostRound] = useState();
    const [returnTrade, setReturnTrade] = useState();

    useEffect(() => {
        if(!localStorage.getItem("token")) {
            alert('로그인 후 이용해주세요.');
            history.push("/");
        }

        const fetchData = async () => {
            const result = await fetch("/api/template/return/all", {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());
    

            if(result.success) {
                const tempArray = [];
                for(let i=0; i<result.data.length; i++) {
                    tempArray.push(
                        <TemplateChild click={onClickTemplate} data={result.data[i]} order={i} key={i}/>
                    )
                    }
                
    
                setTemplate(tempArray);
            }else {
                if(result.code === -1) {
                    alert(result.msg);
                    history.push("/payment");
                }
            }

        }

        fetchData();        
    }, []);

    const onClickTemplate = (e) => {
        selectTemplate(e.currentTarget);
        const customJson = JSON.parse(e.currentTarget.getAttribute("data"));
        console.log(customJson);

        selectIdx(customJson.template_idx);
        setTemplateName(customJson.template_name);
        setReturnDelivery(customJson.return_delivery);
        setReturnCostSingle(customJson.return_cost_single);
        setReturnCostRound(customJson.return_cost_round);
        setReturnTrade(customJson.return_trade);
    }

    const onClickAddTemplate = () => {
        window.location.reload();
    }

    const openDeleteModal = () => {
        setDeleteModal(true);
        setOverlay("block");
    }
    
    const closeDeleteModal = () => {
        setDeleteModal(false);
        setOverlay("none");
    }

    const removeProduct =  async () => {
        if(!selectedIdx) {
            alert('삭제할 템플릿을 선택해주세요.');
            return;
        }

        const result = await fetch("/api/template/return?_method=DELETE", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({selectedIdx})
        }).then(res => res.json());

        if(result.success) {
            alert('삭제 되었습니다.');
            closeDeleteModal();
            window.location.reload();
        }
    }

    const onChangeTemplateName = (e) => {
        setTemplateName(e.target.value);
    }

    const onChangeReturnDelivery = (e) => {
        setReturnDelivery(e.target.value);
    }

    const onChangeReturnCostSingle = (e) => {
        setReturnCostSingle(e.target.value);
    }

    const onChangeReturnCostRound = (e) => {
        setReturnCostRound(e.target.value);
    }

    const onChangeReturnTrade = (e) => {
        setReturnTrade(e.target.value);
    }

    const onCreateReturn = async () => {
        let customJson = {
            templateName: templateName,
            returnDelivery: returnDelivery,
            returnCostSingle: returnCostSingle,
            returnCostRound: returnCostRound,
            returnTrade: returnTrade
        };

        const result = await fetch("/api/template/return", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({requestJson: customJson, selectedIdx})
        }).then(res => res.json());

        if(result.success) {
            alert('템플릿이 등록되었습니다.');
            window.location.reload();
        }else {
            alert('오류가 발생하였습니다.');
        }
    }


    return(
        <div className="template__wrapper">
            <div className="overlay" style={{display: overlay}}></div>
            <h1 className="template__title">반품/교환 템플릿 목록</h1>
            <div className="template__boxes">
                <div className="template__lists">
                    {template}


                    <div className="template__lists--addBtn" onClick={onClickAddTemplate}>템플릿 추가</div>
                </div>

                <div className="template__contents">
                    <div className="template__contents__row">
                        <div className="template__contents__row--title">이름</div>
                        <input type="text" className="template__contents__row--input" value={templateName} onChange={onChangeTemplateName}/>
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">반품/교환 택배처</div>
                        <input type="text" className="template__contents__row--input" placeholder="기본 택배사 (우체국)" value={returnDelivery} onChange={onChangeReturnDelivery}/>
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">반품/교환 배송비(편도)</div>
                        <input type="text" className="template__contents__row--input" value={returnCostSingle} onChange={onChangeReturnCostSingle}/>
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">반품/교환 배송비(왕복)</div>
                        <input type="text" className="template__contents__row--input" value={returnCostRound} onChange={onChangeReturnCostRound}/>
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">반품/교환처</div>
                        <input type="text" className="template__contents__row--input"  value={returnTrade} onChange={onChangeReturnTrade}/>
                    </div>                                                                                
                </div>
            </div>

            <div className="template__action__box">
                    <div className="template__deleteBtn" onClick={openDeleteModal}>
                        삭제
                    </div>
                    <div className="template__createBtn" onClick={onCreateReturn}>
                        등록/수정
                    </div>
            </div>
            <DeletePopup isOpen={deleteModal} close={closeDeleteModal} remove={removeProduct}></DeletePopup>
        </div>
    )
}

export default ReturnTemplate;