import React, { useState, useRef, useEffect } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import imageAddButton from "../../resources/images/image_add.png";
import Select from "react-select";
import UploadPopup from "../utils/uploadImagePopup";
import { useHistory } from "react-router";

function ProductCreate () {

    const history = useHistory();
    const [deliveryTmpList, setDeliveryTmpList] = useState();
    const [returnTmpList, setReturnTmpList] = useState();
    const [imageTmpList, setImageTmpList] = useState();
    const [asTmpList, setAsTmpList] = useState();

    useEffect(() => {
        if(!localStorage.getItem("token")) {
            alert('로그인 후 이용해주세요.');
            history.push("/");
        }

        
        const fetchData = async () => {
            const result = await fetch("/api/template", {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());
    

            if(result.success) {
                let tmpArray = [];

                for(let i=0; i<result.templateImage.length; i++) {
                    tmpArray.push({
                        value: result.templateImage[i].template_idx,
                        label: result.templateImage[i].template_name
                    });
                }
                setImageTmpList(tmpArray);
                tmpArray = [];

                console.log(result);

                for(let i=0; i<result.templateDelivery.length; i++) {
                    tmpArray.push({
                        value: result.templateDelivery[i].template_idx,
                        label: result.templateDelivery[i].template_name
                    });
                }

                setDeliveryTmpList(tmpArray);
                tmpArray = [];

                for(let i=0; i<result.templateReturn.length; i++) {
                    tmpArray.push({
                        value: result.templateReturn[i].template_idx,
                        label: result.templateReturn[i].template_name
                    });
                }

                setReturnTmpList(tmpArray);
                tmpArray = [];

                for(let i=0; i<result.templateAs.length; i++) {
                    tmpArray.push({
                        value: result.templateAs[i].template_idx,
                        label: result.templateAs[i].template_name
                    });
                }

                setAsTmpList(tmpArray);
            }

        }

        

        fetchData();  
    }, [])

    const [uploadModal, setUploadModal] = useState(false);

    const [overlay, setOverlay] = useState("none");

    const openUploadModal = () => {
        setUploadModal(true);
        setOverlay("block");
    }

    const closeUploadModal = () => {
        setUploadModal(false);
        setOverlay("none");
    }

    /** 
     * 
     * TODO: image upload popup 
     */


    const [OptElem, setOptElem] = useState([]);
    const [addElem, setAddElem] = useState([]);
    const [certElem, setCertElem] = useState([]);

    const [productOptions, setProductOptions] = useState([]);
    const [addProductOptions, setAddProductOptions] = useState([]);
    const [certifications, setCertifications] = useState([]);

    const addToOptions = (item) => {
        const optionName = item.target.getAttribute("optionName");
        const optionValue = item.target.getAttribute("optionValue");
        const optionPrice = item.target.getAttribute("optionPrice");
        const optionCount = item.target.getAttribute("optionCount");

        const data = {
            optionName,
            optionValue,
            optionPrice,
            optionCount
        };

        setProductOptions([...productOptions, data]);

        alert('저장되었습니다.');
        console.log(productOptions);
        
    }

    const addProductToOptions = (item) => {
        const optionName = item.target.getAttribute("optionName");
        const optionValue = item.target.getAttribute("optionValue");
        const optionPrice = item.target.getAttribute("optionPrice");
        const optionCount = item.target.getAttribute("optionCount");

        const data = {
            optionName,
            optionValue,
            optionPrice,
            optionCount
        };

        setAddProductOptions([...addProductOptions, data]);

        alert('저장되었습니다.');
        console.log(addProductOptions);
    }


    const addToItem = (item) => {
        //setCertifications([...certifications, item]);
        const certAgency = item.target.getAttribute("certAgency");
        const certNumber = item.target.getAttribute("certNumber");
        const certName = item.target.getAttribute("certName");
        const certDate = item.target.getAttribute("certDate");
        
        const data = {
            certAgency,
            certNumber,
            certName,
            certDate
        };


        setCertifications([...certifications, data])

        alert('저장되었습니다.');
    }


    const appendOptBody = () => {
        setOptElem(oldArray => [...oldArray, <OptBody button="true" addToItem={addToOptions}/>]);
    }

    const appendElemBody = () => {
        setAddElem(oldArray => [...oldArray, <OptBody button="true" addToItem={addProductToOptions} />]);
    }

    const appendCertBody = (e) => {
        //setCertifications

        setCertElem(oldArray => [...oldArray, <CertBody button="false" addToItem={addToItem}/>])
    }

    const topImageDemo = [
        {
            value: 'topImage1', label: 'TopImage1'
        },
        {
            value: 'topImage2', label: 'TopImage2'
        },
        {
            value: 'topImage3', label: 'TopImage3'
        },
        {
            value: 'topImage4', label: 'TopImage4'
        },
        {
            value: 'topImage5', label: 'TopImage5'
        },
    ];

    const [salesState, setSalesState] = useState(true);
    const salesTrueRef = useRef();
    const salesFalseRef = useRef();

    const [optionState, setOptionState] = useState(true);
    const optionTrueRef = useRef();
    const optionFalseRef = useRef();

    const [certState, setCertState] = useState(false);
    const certTrueRef = useRef();
    const certFalseRef = useRef();

    const [minorState, setMinorState] = useState(true);
    const minorTrueRef = useRef();
    const minorFalseRef = useRef();

    const erysipeRef = useRef();
    const mixtypeRef = useRef();

    
    const erysipeAddRef = useRef();
    const mixtypeAddRef = useRef();

    const [addedProductState, setAddedProductState] = useState(true);
    const addedTrueRef = useRef();
    const addedFalseRef = useRef();


    const onErysipeClick = () => {
        erysipeRef.current.classList.add("create__toggle--selected");
        mixtypeRef.current.classList.remove("create__toggle--selected");
    }

    const onMixtypeClick = () => {
        erysipeRef.current.classList.remove("create__toggle--selected");
        mixtypeRef.current.classList.add("create__toggle--selected");
    }

    const onErysipeAddClick = () => {
        erysipeAddRef.current.classList.add("create__toggle--selected");
        mixtypeAddRef.current.classList.remove("create__toggle--selected");
    }

    const onMixtypeAddClick = () => {
        erysipeAddRef.current.classList.remove("create__toggle--selected");
        mixtypeAddRef.current.classList.add("create__toggle--selected");
    }


    const onOptionTrue = () => {
        setOptionState(true);
        optionFalseRef.current.classList.remove("create__toggle--selected");
        optionTrueRef.current.classList.add("create__toggle--selected");
    }

    const onOptionFalse = () => {
        setOptionState(false);
        optionFalseRef.current.classList.add("create__toggle--selected");
        optionTrueRef.current.classList.remove("create__toggle--selected");
    }

    const onAddedTrue = () => {
        setAddedProductState(true);
        addedTrueRef.current.classList.add("create__toggle--selected");
        addedFalseRef.current.classList.remove("create__toggle--selected");
    }

    const onAddedFalse = () => {
        setAddedProductState(false);
        addedTrueRef.current.classList.remove("create__toggle--selected");
        addedFalseRef.current.classList.add("create__toggle--selected");
    }

    const onSalesTrue = () => {
        setSalesState(true);
        salesFalseRef.current.classList.remove("create__toggle--selected");
        salesTrueRef.current.classList.add("create__toggle--selected");
    }

    const onSalesFalse = () => {
        setSalesState(false);
        setSailPrice(0);
        salesFalseRef.current.classList.add("create__toggle--selected");
        salesTrueRef.current.classList.remove("create__toggle--selected");
    }

    const onCertTrue = () => {
        setCertState(true);
        certFalseRef.current.classList.add("create__toggle--selected");
        certTrueRef.current.classList.remove("create__toggle--selected");
    }

    const onCertFalse = () => {
        setCertState(false);
        certFalseRef.current.classList.remove("create__toggle--selected");
        certTrueRef.current.classList.add("create__toggle--selected");
    }

    const onMinorTrue = () => {
        setMinorState(true);
        minorFalseRef.current.classList.remove("create__toggle--selected");
        minorTrueRef.current.classList.add("create__toggle--selected");
        setCanMinor("가능");
    }

    const onMinorFalse = () => {
        setMinorState(false);
        minorFalseRef.current.classList.add("create__toggle--selected");
        minorTrueRef.current.classList.remove("create__toggle--selected");
        setCanMinor("불가능");
    }


    const [wonsanSelectedState, setWonsanSelectedState] = useState("국산");

    const wonsanClick = (e) => {
        const newSelctedWonsan = e.currentTarget.getAttribute("value");
        setWonsanSelectedState(newSelctedWonsan);
    }

    const [taxSelectedState, setTaxSelectedState] = useState("과세상품");

    const taxClick = (e) => {
        const newSelectedTax = e.currentTarget.getAttribute("value");
        setTaxSelectedState(newSelectedTax);
    }

    const [category, setCategory] = useState();
    const [productName, setProductName] = useState();
    const [purchasePrice, setPurchasePrice] = useState();
    const [productMargin, setProductMargin] = useState();
    const [sailPrice, setSailPrice] = useState(0);
    const [avCount, setAvCount] = useState();
    const [remark, setRemark] = useState();

    // images

    const [mainImage, setMainImage] = useState();
    const [mainThumbnail, setMainThumbnail] = useState(imageAddButton);
    const [subImage1, setSubImage1] = useState();
    const [sub1Thumbnail, setSub1Thumbnail] = useState(imageAddButton);
    const [subImage2, setSubImage2] = useState();
    const [sub2Thumbnail, setSub2Thumbnail] = useState(imageAddButton);
    const [subImage3, setSubImage3] = useState();
    const [sub3Thumbnail, setSub3Thumbnail] = useState(imageAddButton);
    const [detailImage, setDetailImage] = useState();
    const [detailThumbnail, setDetailThumbnail] = useState(imageAddButton);

    const [selectedTopImage, selectTopImage] = useState();
    const [selectedBottomImage, selectBottomImage] = useState();

    const [selectedDelivery, selectDelivery] = useState();
    const [selectedReturn, selectReturn] = useState();
    const [selectedAs, selectAs] = useState();

    const mainImageRef = useRef();
    const subImage1Ref = useRef();
    const subImage2Ref = useRef();
    const subImage3Ref = useRef();
    const detailImageRef = useRef();

    const onChangeCategory = (e) => {
        setCategory(e.target.value);
    }

    const onChangeProductName = (e) => {
        setProductName(e.target.value);
    }

    const onChangePurchasePrice = (e) => {
        setPurchasePrice(e.target.value);
    
    }

    const onChangeProductMargin = (e) => {
        setProductMargin(e.target.value);
    }

    const onChangeSailPrice = (e) => {
        setSailPrice(e.target.value);
    }

    const onChangeAvCount = (e) => {
        setAvCount(e.target.value);
    }


    const onChangeRemark = (e) => {
        setRemark(e.target.value);
    }

    // images

    const mainImageClick = () => {
        mainImageRef.current.click();
    }

    const subImage1Click = () => {
        subImage1Ref.current.click();
    }

    const subImage2Click = () => {
        subImage2Ref.current.click();
    }

    const subImage3Click = () => {
        subImage3Ref.current.click();
    }

    const detailImageClick = () => {
        detailImageRef.current.click();
    }

    const mainImageChange = (e) => {
        const file = e.dataTransfer ? e.dataTransfer.files[0] : e.currentTarget.files[0];
        setMainThumbnail(URL.createObjectURL(file));
        setMainImage(e.target.files[0]);
    }

    const subImage1Change = (e) => {
        const file = e.dataTransfer ? e.dataTransfer.files[0] : e.currentTarget.files[0];
        setSub1Thumbnail(URL.createObjectURL(file));
        setSubImage1(e.target.files[0]);
    }

    const subImage2Change = (e) => {
        const file = e.dataTransfer ? e.dataTransfer.files[0] : e.currentTarget.files[0];
        setSub2Thumbnail(URL.createObjectURL(file));
        setSubImage2(e.target.files[0]);
    }

    const subImage3Change = (e) => {
        const file = e.dataTransfer ? e.dataTransfer.files[0] : e.currentTarget.files[0];
        setSub3Thumbnail(URL.createObjectURL(file));
        setSubImage3(e.target.files[0]);
    }

    const detailImageChange = (e) => {
        const file = e.dataTransfer ? e.dataTransfer.files[0] : e.currentTarget.files[0];
        setDetailThumbnail(URL.createObjectURL(file));
        setDetailImage(e.target.files[0]);
    }

    const onChangeImageTemplateTop = (e) => {
        selectTopImage(e.value);
    }

    
    const onChangeImageTemplateBottom = (e) => {
        selectBottomImage(e.value);
    }

    const onChangeDeliveryTemplate = (e) => {
        selectDelivery(e.value);
    }

    const onChangeReturnTemplate = (e) => {
        selectReturn(e.value);
    }

    const onChangeAsTemplate = (e) => {
        selectAs(e.value);
    }

    const onChangeProductModel = (e) => {
        setProductModel(e.target.value);
    }

    const onChangeProductBrand = (e) => {
        setProductBrand(e.target.value);
    }

    const onChangeProductMade = (e) => {
        setProductMade(e.target.value);
    }

    const onChangeProductInfoText = (e) => {
        setProductInfoText(e.target.value);
    }


    const productCreate = async () => {
        let defaultInfo = {
            category: category,
            productName: productName,
            purchasePrice: purchasePrice,
            productMargin: productMargin,
            sailPrice: sailPrice,
            avCount: avCount,
            remark: remark
        };

        let imagesInfo = {
            mainImage: mainImage,
            subImage1: subImage1,
            subImage2: subImage2,
            subImage3: subImage3,
            detailImage: detailImage,
            templateImageTop: selectedTopImage,
            templateImageBottom: selectedBottomImage
        }

        let options = JSON.stringify(productOptions)

        let addOptions = JSON.stringify(addProductOptions)
        
        let productInfo = {
            productModel: productModel,
            productBrand: productBrand,
            productMade: productMade,
            productOrigin: wonsanSelectedState,
            certifications: JSON.stringify(certifications),
            canMinor: canMinor,
            taxType: taxSelectedState,
            productInfoText: productInfoText,
            templateDelivery: selectedDelivery,
            templateReturn: selectedReturn,
            templateAs: selectedAs
        }

        const formData = new FormData();

        formData.append("category", category);
        formData.append("productName", productName);
        formData.append("purchasePrice", purchasePrice);
        formData.append("productMargin", productMargin);
        formData.append("sailPrice", sailPrice);
        formData.append("avCount", avCount);
        formData.append("remark", remark);
        formData.append("mainImage", mainImage);
        formData.append("subImage1", subImage1);
        formData.append("subImage2", subImage2);
        formData.append("subImage3", subImage3);
        formData.append("detailImage", detailImage);
        formData.append("templateImageTop", selectedTopImage);
        formData.append("templateImageBottom", selectedBottomImage);
        formData.append("options", JSON.stringify(productOptions));
        formData.append("addProductOptions", JSON.stringify(addProductOptions));
        formData.append("productModel", productModel);
        formData.append("productBrand", productBrand);
        formData.append("productMade", productMade);
        formData.append("productOrigin", wonsanSelectedState);
        formData.append("certifications", JSON.stringify(certifications));
        formData.append("canMinor", canMinor);
        formData.append("taxType", taxSelectedState);
        formData.append("productInfoText", productInfoText);
        formData.append("templateDelivery", selectedDelivery);
        formData.append("templateReturn", selectedReturn);
        formData.append("templateAs", selectedAs);

        const result = await fetch("/api/product", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`
            },
            method: "POST",
            body: formData
        }).then(res => res.json());

        console.log(result);

        if(result.success) {
            alert('상품이 등록되었습니다.');
            history.push("/productList");
        }


    }

    const [productModel, setProductModel] = useState();
    const [productBrand, setProductBrand] = useState();
    const [productMade, setProductMade] = useState();

    const [canMinor, setCanMinor] = useState("가능");
    const [productInfoText, setProductInfoText] = useState();



    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];
    return(
        <div className={classnames("container", "createContainer")}>
            <div className="overlay" style={{display: overlay}} />
            <Header type="productCreate"></Header>
                <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="productList" sidebarLinks={sidebarLinks}></Sidebar>
                <div className="product__wrapper--create">
                <div className="createHeader">
                    <div className="createHeader__title">상품 기본 정보</div>
                </div>

                <div className={classnames("createDefaultInfo", "createSection")}>
                    <table>
                        <tr>
                            <td className="create__optionName">카테고리</td>
                            <td className="create__optionInput" ><input type="text" placeholder="ex) 쿠팡" value={category} onChange={onChangeCategory}/></td>
                        </tr>
                        <tr>
                            <td className="create__optionName">상품명</td>
                            <td className="create__optionInput"><input type="text" value={productName} onChange={onChangeProductName} /></td>
                        </tr>
                        <tr>
                            <td className="create__optionName">매입가</td>
                            <td className="create__optionInput"><input type="text" value={purchasePrice} onChange={onChangePurchasePrice}/>원</td>
                        </tr>
                        <tr>
                            <td className="create__optionName">마진율</td>
                            <td className="create__optionInput"><input type="text" value={productMargin} onChange={onChangeProductMargin}/>원 / %</td>
                        </tr>
                        <tr>
                            <td className="create__optionName">할인</td>
                            <td className="create__optionToggle">
                                <div className="toggle__box">
                                    <div className={classnames('create__sales--btn', 'create__toggle--selected')} onClick={onSalesTrue} ref={salesTrueRef}>설정함</div>
                                    <div className={classnames('create__sales--btn')} onClick={onSalesFalse} ref={salesFalseRef}>설정안함</div>
                                </div>
                            </td>
                        </tr>
                        {salesState ? 
                            <tr>
                                <td className={classnames("create__optionName", "create__optionName--active")}>
                                    할인가
                                </td>
                                <td className={classnames("create__optionInput", "create__optionInput--active")}>
                                    <input type="text" value={sailPrice} onChange={onChangeSailPrice}/>원 / %
                                </td>
                            </tr>
                        : null}
                        <tr>
                            <td className="create__optionName">
                                재고 수량
                            </td>
                            <td className="create__optionInput">
                                <input type="text" value={avCount} onChange={onChangeAvCount}/>개
                            </td>
                        </tr>

                        <tr>
                            <td className="create__optionName">
                                비고
                            </td>
                            <td className="create__optionInput">
                                <input type="text" className="create__remark--textarea" value={remark} onChange={onChangeRemark}/>
                            </td>
                        </tr>
                    </table>
                </div>
                           
                <div className="createHeader">
                    <div className="createHeader__title">상품 이미지</div>
                </div>

                <div className={classnames("createProductImage", "createSection")}>
                    <table>
                        <tr className="flexible">
                            <td className="create__optionName">대표 이미지</td>
                            <td className="create__optionInput">
                                <img src={mainThumbnail} onClick={mainImageClick} className="thumbnail__image"/>
                                <input type="file" hidden ref={mainImageRef} onChange={mainImageChange}/>
                            </td>
                        </tr>

                        <tr className="flexible">
                            <td className="create__optionName">추가 이미지</td>
                            <td className="create__optionInput">
                                <img src={sub1Thumbnail} className="thumbnail__image" onClick={subImage1Click}/>
                                <input type="file" hidden ref={subImage1Ref} onChange={subImage1Change}/>
                                <img src={sub2Thumbnail} className="thumbnail__image" onClick={subImage2Click}/>
                                <input type="file" hidden ref={subImage2Ref} onChange={subImage2Change}/>
                                <img src={sub3Thumbnail} className="thumbnail__image" onClick={subImage3Click} />
                                <input type="file" hidden ref={subImage3Ref} onChange={subImage3Change}/>
                            </td>
                        </tr>

                        <tr className="flexible">
                            <td className="create__optionName">상단 이미지</td>
                            <td className="create__optionInput" style={{width: "376px"}}>
                                <Select options={imageTmpList} onChange={onChangeImageTemplateTop}/>
                            </td>
                        </tr>

                        
                        <tr className="flexible">
                            <td className="create__optionName">상세 이미지</td>
                            <td className="create__optionInput">
                                <img src={detailThumbnail} className="thumbnail__image"  onClick={detailImageClick} />
                                <input type="file" hidden ref={detailImageRef} onChange={detailImageChange}/>
                            </td>
                        </tr>

                        <tr className="flexible">
                            <td className="create__optionName">하단 이미지</td>
                            <td className="create__optionInput" style={{width: "376px"}}>
                                <Select options={imageTmpList} onChange={onChangeImageTemplateBottom}/>
                            </td>
                        </tr>

                    </table>                        
                </div>

                <div className="createHeader">
                    <div className="createHeader__title">옵션</div>
                </div>

                <div className={classnames("createProductImage", "createSection", "not-mgbt")}>
                    <div className="divTable">
                        <div className="toggleBox">
                            <div className="create__optionName">옵션</div>
                            <div className="create__optionToggle">
                                <div className="toggle__box">
                                    <div className={classnames('create__sales--btn', 'create__toggle--selected')} onClick={onOptionTrue} ref={optionTrueRef}>설정함</div>
                                    <div className={classnames('create__sales--btn')} onClick={onOptionFalse} ref={optionFalseRef}>설정안함</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {optionState ?
                    <div className={classnames("createProductImage", "createSection", "innerTableSection")}>
                        <div className="divTable">
                            <div className="toggleBox">
                                <div className="create__optionName">유형</div>
                                <div className="create__optionToggle">
                                    <div className="toggle__box">
                                        <div className={classnames('create__sales--btn', 'create__toggle--selected', 'erysipe__toggle--btn')} ref={erysipeRef} onClick={onErysipeClick}>단독형</div>
                                        <div className={classnames('create__sales--btn', 'mixtype__toggle--btn')} ref={mixtypeRef} onClick={onMixtypeClick}>조합형</div>
                                    </div>
                                </div>
                            </div>
                            
                            <table className="optionTable" style={{textAlign: "center"}}>
                                <thead>
                                    <tr>
                                        <th>옵션명</th>
                                        <th>옵션값</th>
                                        <th>옵션가(판매가 대비)</th>
                                        <th>재고</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {OptElem}
                                </tbody>
                            </table>
                            
                            <button className="newOptionBtn" onClick={appendOptBody}>옵션 추가</button>
                        </div>
                    </div>
                : null}

                <div className="createHeader">
                    <div className="createHeader__title">추가상품</div>
                </div>


                <div className={classnames("createProductImage", "createSection", "not-mgbt")}>
                    <div className="divTable">
                        <div className="toggleBox">
                            <div className="create__optionName">추가</div>
                            <div className="create__optionToggle">
                                <div className="toggle__box">
                                    <div className={classnames('create__sales--btn', 'create__toggle--selected')} onClick={onAddedTrue} ref={addedTrueRef}>설정함</div>
                                    <div className={classnames('create__sales--btn')} onClick={onAddedFalse} ref={addedFalseRef}>설정안함</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {addedProductState ?
                    <div className={classnames("createProductImage", "createSection", "innerTableSection")}>
                        <div className="divTable">
                            <div className="toggleBox">
                                <div className="create__optionName">유형</div>
                                <div className="create__optionToggle">
                                    <div className="toggle__box">
                                        <div className={classnames('create__sales--btn', 'create__toggle--selected', 'erysipe__toggle--btn')} ref={erysipeAddRef} onClick={onErysipeAddClick}>단독형</div>
                                        <div className={classnames('create__sales--btn', 'mixtype__toggle--btn')} ref={mixtypeAddRef} onClick={onMixtypeAddClick}>조합형</div>
                                    </div>
                                </div>
                            </div>
                            
                            <table className="optionTable" style={{textAlign: "center"}}>
                                <thead>
                                    <tr>
                                        <th>옵션명</th>
                                        <th>옵션값</th>
                                        <th>옵션가(판매가 대비)</th>
                                        <th>재고</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {addElem}
                                </tbody>
                            </table>
                            
                            <button className="newOptionBtn" onClick={appendElemBody}>옵션 추가</button>
                        </div>
                    </div>
                : null}                

                <div className="createHeader">
                    <div className="createHeader__title">상품 주요 정보</div>
                </div>

                <div className={classnames("createDefaultInfo", "createSection", "defaultMgBottom")}>
                    <table>
                        <tr>
                            <td className="create__optionName">모델명</td>
                            <td className="create__optionInput"><input type="text" value={productModel} onChange={onChangeProductModel} /></td>
                        </tr>
                        <tr>
                            <td className="create__optionName">브랜드</td>
                            <td className="create__optionInput"><input type="text" value={productBrand} onChange={onChangeProductBrand} /></td>
                        </tr>
                        <tr>
                            <td className="create__optionName">제조사</td>
                            <td className="create__optionInput"><input type="text" value={productMade} onChange={onChangeProductMade}/></td>
                        </tr>
                        <tr>
                            <td className="create__optionName">원산지</td>
                            <td className="create__optionInput">
                                <div className={classnames("flexibleColumn", "productInputMg")}>
                                    <div className={wonsanSelectedState === "국산" ? "multipple__toggle--selected" : "multipple__toggle"} value="국산" onClick={wonsanClick} id="wonsan1">국산</div>
                                    <div className={wonsanSelectedState === "수입산" ? "multipple__toggle--selected" : "multipple__toggle"} value="수입산" onClick={wonsanClick} id="wonsan2">수입산</div>
                                    <div className={wonsanSelectedState === "기타" ? "multipple__toggle--selected" : "multipple__toggle"} value="기타" onClick={wonsanClick} id="wonsan3">기타</div>
                                    <div className={wonsanSelectedState === "의무대상아님" ? "multipple__toggle--selected" : "multipple__toggle"} value="의무대상아님" onClick={wonsanClick} id="wonsan4">원산지 표기 의무대상 아님</div>
                                </div>
                            </td>
                        </tr>                        
                    </table>                
            
                </div>      
                    {/* {wonsanSelectedState === "국산" ?
                        <div className={classnames("createProductImage", "createSection", "innerTableSection", "multipleCombo", "not-mgbt")}>
                            <div className="selectBoxModels" style={{width: '214px'}}>
                                <Select options={topImageDemo} placeholder="지역 대분류"/>
                            </div>
                            <div className="selectBoxModels" style={{width: '214px'}}>
                                <Select options={topImageDemo} placeholder="지역 소분류"/>
                            </div>
                        </div>            

                    : null} */}

                    <div className={classnames("createProductImage", "createSection", "not-mgbt")}>
                        <div className="divTable">
                            <div className="toggleBox">
                                <div className="create__optionName">인증</div>
                                <div className="create__optionToggle">
                                    <div className="toggle__box">
                                        <div className={classnames('create__sales--btn', 'create__toggle--selected')} onClick={onCertFalse} ref={certTrueRef}>선택안함</div>
                                        <div className={classnames('create__sales--btn')} onClick={onCertTrue} ref={certFalseRef}>인증카테고리</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    {certState ?
                    <div className={classnames("createProductImage", "createSection", "innerTableSection")}>
                        <div className="divTable">

                            
                            <table className="optionTable" style={{textAlign: "center"}}>
                                <thead>
                                    <tr>
                                        <th>인증기관</th>
                                        <th>인증번호</th>
                                        <th>인증상호</th>
                                        <th>인증일자</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {certElem}
                                </tbody>
                            </table>
                            
                            <button className="newOptionBtn" onClick={appendCertBody}>옵션 추가</button>
                        </div>
                    </div>
                : null}         

                <div className={classnames("createProductImage", "createSection", "not-mgbt")}>
                    <div className="divTable">
                        <div className="toggleBox">
                            <div className="create__optionName">미성년자 구매</div>
                            <div className="create__optionToggle">
                                <div className="toggle__box">
                                    <div className={classnames('create__sales--btn', 'create__toggle--selected')} onClick={onMinorTrue} ref={minorTrueRef}>가능</div>
                                    <div className={classnames('create__sales--btn')} onClick={onMinorFalse} ref={minorFalseRef}>불가능</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>            

                <div className={classnames("createProductImage", "createSection", "not-mgbt")}>
                    <div className="divTable">
                        <div className="toggleBox">
                            <div className="create__optionName">부가세</div>
                            <div className="create__optionToggle">
                                <div className={classnames("flexibleColumn", "productInputMg")}>
                                        <div className={taxSelectedState === "과세상품" ? "multipple__toggle--selected" : "multipple__toggle"} value="과세상품" onClick={taxClick} id="wonsan1">과세상품</div>
                                        <div className={taxSelectedState === "면세상품" ? "multipple__toggle--selected" : "multipple__toggle"} value="면세상품" onClick={taxClick} id="wonsan2">면세상품</div>
                                        <div className={taxSelectedState === "영세상품" ? "multipple__toggle--selected" : "multipple__toggle"} value="영세상품" onClick={taxClick} id="wonsan3">영세상품</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>   

                <div className={classnames("createDefaultInfo", "createSection", "defaultMgBottom", "not-mgbt")}>
                    <table className="paddingLf10">
                        <tr>
                            <td className="create__optionName">상품정보 제공고시</td>
                            <td className="create__optionTextarea"><textarea placeholder="기입하지 않을 경우 상품 상품 상세페이지 참조로 자동 기입" value={productInfoText} onChange={onChangeProductInfoText}></textarea></td>
                        </tr>                         
                    </table>
                </div>        

                
                <div className={classnames("createDefaultInfo", "createSection", "defaultMgBottom")}>
                    <table className="paddingLf10">
                        <tr className="flexible">
                            <td className="create__optionName" style={{padding: 0}}>배송</td>
                            <td className="create__optionInput" style={{width: "376px"}}>
                                <Select options={deliveryTmpList} placeholder="배송 템플릿 목록" onChange={onChangeDeliveryTemplate}/>
                            </td>
                        </tr>                        
                        
                        <tr className="flexible">
                            <td className="create__optionName" style={{padding: 0}}>반품/교환</td>
                            <td className="create__optionInput" style={{width: "376px"}}>
                                <Select options={returnTmpList} placeholder="반품/교환 템플릿 목록" onChange={onChangeReturnTemplate}/>
                            </td>
                        </tr>     

                        <tr className="flexible">
                            <td className="create__optionName" style={{padding: 0}}>A/S특이사항</td>
                            <td className="create__optionInput" style={{width: "376px"}}>
                                <Select options={asTmpList} placeholder="A/S 특이사항 템플릿 목록" onChange={onChangeAsTemplate}/>
                            </td>
                        </tr>                                                     
                    </table>
                </div>       

                <div className="endLine"/>      
                
                <div className="action__box">
                    <div className="createCancelBtn">취소</div>
                    <div className="createBtn" onClick={productCreate}>등록/수정</div>
                </div>
                             

            </div>
            </div>
        </div>

    )
}




function OptElemChild(props) {
    const {addToOptions} = props;
    return(
        <input type="text" className="optionInput" />
    )
}

function OptBody(props) {
    const {addToItem} = props;
    const [OptElemInput, SetOptElemInput] = useState([]);

    const [optionName, setOptionName] = useState();
    const [optionValue, setOptionValue] = useState([]);
    const [optionPrice, setOptionPrice] = useState();
    const [optionCount, setOptionCount] = useState();


    const onChangeOptionName = (e) => {
        setOptionName(e.target.value);
    }

    const onChangeOptionValue = (e) => {
        setOptionValue(e.target.value);
    }

    const onChangeOptionPrice = (e) => {
        setOptionPrice(e.target.value);
    }

    const onChangeOptionCount = (e) => {
        setOptionCount(e.target.value);
    }

    const appendOptChild = (e) => {

        //SetOptElemInput(oldArray => [...oldArray, <OptElemChild />]);

        setChildOption([...childOption, {
            value: optionValue,
            label: optionValue
        }]);

        console.log(childOption);
    }

    const [childOption, setChildOption] = useState([]);



    return(
        <tr>
            <td>
                <input type="text" className="optionInput" value={optionName} onChange={onChangeOptionName} />
            </td>
            <td>
                <Select options={childOption} placeholder="옵션 추가된 항목이 보입니다"/>
                <input type="text" style={{marginTop: "20px"}} className="optionInput" value={optionValue} onChange={onChangeOptionValue}/>
                {OptElemInput}
                {props.button === "true" ? 
                    <>
                        <button className="optionAddButton" onClick={appendOptChild}>추가</button>
                    </>
                : null}
            </td>
            <td>
                <input type="text" className="optionInput" value={optionPrice} onChange={onChangeOptionPrice}/>
            </td>
            <td>
                <input type="text" className="optionInput" value={optionCount} onChange={onChangeOptionCount}/>
                <button className="optionAddButton" onClick={addToItem} optionName={optionName} optionValue={JSON.stringify(childOption)} optionPrice={optionPrice} optionCount={optionCount}>저장</button>
            </td>
        </tr>
    )
}

function CertBody(props) {
    const {addToItem} = props;
    const [certAgency, setCertAgency] = useState();
    const [certNumber, setCertNumber] = useState();
    const [certName, setCertName] = useState();
    const [certDate, setCertDate] = useState();

    const onChangeCertAgency = (e) => {
        setCertAgency(e.target.value);
    }

    const onChangeCertNumber = (e) => {
        setCertNumber(e.target.value);
    }

    const onChangeCertName = (e) => {
        setCertName(e.target.value);
    }

    const onChangeCertDate = (e) => {
        setCertDate(e.target.value);
    }
    return(
        <tr>
            <td>
                <input type="text" className="optionInput" value={certAgency} onChange={onChangeCertAgency}/>
            </td>
            <td>
                <input type="text" className="optionInput" value={certNumber} onChange={onChangeCertNumber}/>
            </td>
            <td>
                <input type="text" className="optionInput" value={certName} onChange={onChangeCertName}/>
            </td>
            <td>
                <input type="text" className="optionInput" value={certDate} onChange={onChangeCertDate}/>
                <button type="button" className="optionAddButton" onClick={addToItem} certAgency={certAgency} certNumber={certNumber} certName={certName} certDate={certDate}>저장</button>
            </td>
        </tr>
    )
}

export default ProductCreate;