import React, { useState, useEffect } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardNav from "../partials/BoardNav";
import QuillEditor from "../partials/EditorComponent";
import Select from "react-select";
import { useHistory } from "react-router";
function Edit(props) {
    const history = useHistory();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [category, setCategory] = useState();
    const [options, setOptions] = useState();
    const [post, setPost] = useState();
    const [userType, setUserType] = useState();

    const params = new URLSearchParams(props.location.search);
    const board_type = Number(params.get("board_type"));
    const post_idx = Number(params.get("post_idx"));

    useEffect(async () => {
        if(!localStorage.getItem("token")) {
            alert("로그인을 해주세요");
            history.push("/");
        }else {
            const result = await fetch("/api/board/options", {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());

            setOptions(result.option);

            const result2 = await fetch(`/api/board/view?board_type=${board_type}&post_idx=${post_idx}`, {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(response => response.json());
    
            if(result2.success) {

                console.log(result2);
            
                if(result2.data[0].board_type === 1) setCategory({value: 1, label: '공지사항'});
                else if(result2.data[0].board_type === 2) setCategory({value: 2, label: '문의/답변' });
                else if(result2.data[0].board_type === 3) setCategory({value: 3, label: '시스템 오류'});
                else if(result2.data[0].board_type ===4) setCategory({value: 4, label: '시스템 개선 요청'});
        
                setPost(result2.data[0]);
                setUserType(result2.type);
                setTitle(result2.data[0].title);
                setContent(result2.data[0].contents);
            }else {
                alert('본인만 수정 가능합니다.');
                history.goBack();
            }
        }
    }, []);

    const onChangeTitle = (e) => {
        setTitle(e.target.value);
    }
    const editorChange = (value) => {
        setContent(value);
    }

    const categoryChange = (selectedOption) => {
        console.log(category.value);
        setCategory(selectedOption);
    }

    const onBackButtonClick = () => {
        history.goBack();
    }
    
    const onPostUpdate = async () => {
        if(category.value === 0) {
            alert('카테고리를 선택 해주세요.');
            return;
        }

        if(!title) {
            alert("제목을 입력해주세요.");
            return;
        }

        if(!content) {
            alert("내용을 입력해주세요.");
            return;
        }
        const result = await fetch("/api/board?_method=PUT", {
            headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${localStorage.getItem("token")}`
            },
            method: "POST",
            body: JSON.stringify({
                board_type: board_type,
                post_idx: post_idx,
                title: title,
                contents: content
            })
        }).then(response => response.json());

        if(result.success) {
            alert("글이 수정되었습니다.");
            history.goBack();
        }else {
            alert("오류가 발생하였습니다.");
            return;
        }
    }



    const navList = ["글 수정"];
    const navURL = ["/board/write"];
    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];
    return (
        <div className="container">
            <Header type="boardList"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="boards" sidebarLinks={sidebarLinks}></Sidebar>
                <BoardNav selectedNav="글 수정" navList={navList} navURL={navURL} single="true">
                    <div className="category" style={{width: "200px", marginLeft: "15px", marginBottom: "15px", marginRight: "15px"}}>                        
                            <Select value={category} onChange={categoryChange} options={options} placeholder="카테고리" style={{marginRight: '30px'}}/>
                            
                    </div>
                    <div className="wrapper__editor">
                        <textarea className="editor__title--input" placeholder="제목을 입력하세요" value={title} onChange={onChangeTitle}></textarea>
                    </div>
                    <QuillEditor value={content} onChange={editorChange}/>
                    
                    <div className="editor__footer" style={{textAlign: "end", paddingRight: "5px", paddingBottom: "40px"}}>
                        <button className="lists__processButton lists__createButton" onClick={onBackButtonClick}>목록</button>
                        <button className="lists__processButton lists__createButton" onClick={onPostUpdate}>수정</button>
                    </div>
                </BoardNav>
            </div>
        </div>
    )
}

export default Edit;