import React, {useEffect} from "react";
import {Link, useHistory} from "react-router-dom";
import classnames from "classnames";

import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import basketImage from "../../resources/images/basket.png";
import userImage from "../../resources/images/user.png";
import settingsImage from "../../resources/images/settings.png";
function Manage() {
    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];

    const history = useHistory();
    useEffect(() => {
        if(!localStorage.getItem("token")) {
            alert("로그인을 해주세요");
            history.push("/");
        }else {
            fetch("/visit", {
                method: "GET"
            }).then(res => res.json()).then(result => console.log(result));
        }
    }, []);
    return(
        <div className="container">
            <Header></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="manage" sidebarLinks={sidebarLinks}></Sidebar>
                <div className="product__wrapper--create">
                    <div className="manage">
                        <div className="manage__columns">
                            <div className={classnames('manage__column', 'manage__column--main')}>
                                <h1 className={classnames('manage__column--title', 'manage__column--main--title')}>
                                    등록 상품
                                </h1>
                                <Link to="/productList">
                                    <button className={classnames("manage__column__count--btn", "manage__column__basic--btn")}>자세히 보기</button>
                                </Link>
                                <img src={basketImage} className="manage__column--img"/>
                            </div>

                            <div className={classnames('manage__column')}>
                            
                                <h1 className={classnames('manage__column--title')}>
                                    게시판
                                </h1>
                            

                                <Link to="/board/notice">
                                    <button className="manage__column__basic--btn">자세히 보기</button>
                                </Link>
                                <img src={userImage} className="manage__column--img"/>
                            </div>
                        </div>

                        <div className="manage__columns">
                            <div className={classnames('manage__column')}>
                                    <h1 className={classnames('manage__column--title')}>
                                        준비중
                                    </h1>

                                    <button className="manage__column__basic--btn">자세히 보기</button>
                                    <img src={settingsImage} className="manage__column--img"/>
                                </div>

                                <div className={classnames('manage__column')}>
                                    <h1 className={classnames('manage__column--title')}>
                                        준비중
                                    </h1>

                                    <button className="manage__column__basic--btn">자세히 보기</button>
                                    <img src={settingsImage} className="manage__column--img"/>
                                </div>
                            </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Manage;