import React, { useState, useEffect } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardList from "../partials/BoardList";
import { useHistory } from "react-router";

function AdminPayment() {

    const [paymentList, setPaymentList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [postsPerPage, setPostsPerPage] = useState(10);

    const history = useHistory();
    useEffect(async () => {

        if(!localStorage.getItem("token")) {
            alert("로그인을 해주세요");
            history.push("/");
        }else {
            const paymentData = await fetch("/api/admin/payment", {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
                
            }).then(result => result.json());

            if(paymentData.success) {
                setPaymentList(paymentData.paymentList);
            }else {
                alert('권한이 없습니다.');
                history.push("/manage");
            }
            
        }

    }, []);

    
    let indexOfLast = currentPage * postsPerPage;
    let indexOfFirst = indexOfLast - postsPerPage;
    const currentPosts = paymentList.slice(indexOfFirst, indexOfLast);


    const navList = ["UUID"];
    const navURL = ["/admin/paymentHistory"];
    const sidebarList = ["관리페이지", "게시판", "메인 사진 등록", "결제 이력"];
    const sidebarLinks = ["/admin", "/board/notice", "/admin/ads", "/admin/paymentHistory"];
    return (
        <div className="container">
            <Header type="paymentHistory"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} sidebarLinks={sidebarLinks} selectedItem="boards"></Sidebar>
                <BoardList selectedNav="UUID" navList={navList} navURL={navURL} admin="true" postsPerPage={postsPerPage} boardData={currentPosts} paginate={setCurrentPage} totalPosts={paymentList.length} currentPage={currentPage}></BoardList>
            </div>
        </div>
    )
}

export default AdminPayment;