import React, { useState, useEffect } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardNav from "../partials/BoardNav";
import QuillEditor from "../partials/EditorComponent";
import Select from "react-select";
import { useHistory } from "react-router";
function Write() {
    const history = useHistory();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [category, setCategory] = useState({value: 0, label: '카테고리'});
    const [options, setOptions] = useState();

    useEffect(async () => {
        if(!localStorage.getItem("token")) {
            alert("로그인을 해주세요");
            history.push("/");
        }else {
            const result = await fetch("/api/board/options", {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());

            setOptions(result.option);
        }
    }, []);

    const onChangeTitle = (e) => {
        setTitle(e.target.value);
    }
    const editorChange = (value) => {
        setContent(value);
    }

    const categoryChange = (selectedOption) => {
        console.log(category.value);
        setCategory(selectedOption);
    }

    const onBackButtonClick = () => {
        history.goBack();
    }
    
    const onPostButtonClick = async () => {
        if(category.value === 0) {
            alert('카테고리를 선택 해주세요.');
            return;
        }

        if(!title) {
            alert("제목을 입력해주세요.");
            return;
        }

        if(!content) {
            alert("내용을 입력해주세요.");
            return;
        }
        const result = await fetch("/api/board/write", {
            headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${localStorage.getItem("token")}`
            },
            method: "POST",
            body: JSON.stringify({
                board_type: category.value,
                title: title,
                contents: content
            })
        }).then(response => response.json());

        if(result.success) {
            alert("글이 등록되었습니다.");
            history.goBack();
        }else {
            alert("오류가 발생하였습니다.");
            return;
        }

        console.log(result);
    }



    const navList = ["글 쓰기"];
    const navURL = ["/board/write"];
    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];
    return (
        <div className="container">
            <Header type="boardList"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="boards" sidebarLinks={sidebarLinks}></Sidebar>
                <BoardNav selectedNav="글 쓰기" navList={navList} navURL={navURL} single="true">
                    <div className="category" style={{width: "200px", marginLeft: "15px", marginBottom: "15px", marginRight: "15px"}}>                        
                            <Select value={category} onChange={categoryChange} options={options} placeholder="카테고리" style={{marginRight: '30px'}}/>
                            
                    </div>
                    <div className="wrapper__editor">
                        <textarea className="editor__title--input" placeholder="제목을 입력하세요" value={title} onChange={onChangeTitle}></textarea>
                    </div>
                    <QuillEditor value={content} onChange={editorChange}/>
                    
                    <div className="editor__footer" style={{textAlign: "end", paddingRight: "5px", paddingBottom: "40px"}}>
                        <button className="lists__processButton lists__createButton" onClick={onBackButtonClick}>목록</button>
                        <button className="lists__processButton lists__createButton" onClick={onPostButtonClick}>작성</button>
                    </div>
                </BoardNav>
            </div>
        </div>
    )
}

export default Write;