import React, { useState } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardNav from "../partials/BoardNav";
import BoardTable from "../partials/BoardTable";
import Pagination from "../partials/Pagination";
import AdminBoardTable from "./AdminBoardTable";

function BoardList(props) {
    const {selectedNav, navList, navURL, admin, boardData, totalPosts, paginate, postsPerPage, currentPage} = props;
    return(
        <>
            <BoardNav selectedNav={selectedNav} navList={navList} navURL={navURL} admin={admin}>
                {selectedNav === "UUID" ? 
                    <AdminBoardTable boardData={boardData}></AdminBoardTable>
                : <BoardTable boardData={boardData}></BoardTable>}
                
                <Pagination pdTable="154px" totalPosts={totalPosts} paginate={paginate} postsPerPage={postsPerPage} currentPage={currentPage} ></Pagination>
            </BoardNav>
        </>
    )
}

export default BoardList;