import React from "react";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import App from "./components/main/App";
import Manage from "./components/manage/Manage";
import Payment from "./components/manage/Payment";
import ProductCreate from "./components/product/ProductCreate";
import ProductList from "./components/product/ProductList";
import Notice from "./components/boards/Notice";
import Question from "./components/boards/Question";
import Issue from "./components/boards/Issue";
import Request from "./components/boards/Request";
import AdminMain from "./components/admin/AdminMain";
import AdminPayment from "./components/admin/AdminPayment";
import AdminAds from "./components/admin/AdminAds";
import Delivery from "./components/templates/delivery";
import Return from "./components/templates/return";
import AS from "./components/templates/as";
import Images from "./components/templates/images";
import NaverLogin from "./components/login/NaverLogin";
import Write from "./components/boards/Write";
import BoardView from "./components/boards/BoardView";
import Edit from "./components/boards/Edit";
import ProductUpdate from "./components/product/ProductUpdate";

function Home() {
    return(
        <Router>
            <Switch>
                <Route exact path="/" component={App} />
                <Route exact path="/manage" component={Manage} />
                <Route exact path="/payment" component={Payment} />
                <Route exact path="/productList" component={ProductList} />
                <Route exact path="/productCreate" component={ProductCreate} />
                <Route exact path="/productUpdate" component={ProductUpdate} />
                <Route exact path="/board/notice" component={Notice} />
                <Route exact path="/board/question" component={Question} />
                <Route exact path="/board/issue" component={Issue} />
                <Route exact path="/board/request" component={Request} />
                <Route exact path="/board/write" component={Write} />
                <Route exact path="/board/edit" component={Edit} />
                <Route exact path="/board/view" component={BoardView} />
                <Route exact path="/admin" component={AdminMain} />
                <Route exact path="/admin/paymentHistory" component={AdminPayment} />
                <Route exact path="/admin/ads" component={AdminAds} />
                <Route exact path="/template/delivery" component={Delivery} />
                <Route exact path="/template/return" component={Return} />
                <Route exact path="/template/as" component={AS} />
                <Route exact path="/template/images" component={Images} />
                <Route exact path="/auth/naver" component={NaverLogin} />
            </Switch>
        </Router>
    )
}

export default Home;