import React, {useState, useRef, useEffect} from "react";
import { useHistory } from "react-router";
import DeletePopup from "../utils/deletePopup";

function TemplateChild (props)  {
    const {click, data, order } = props;
    return(
        <>
            <div className="template__lists--list" onClick={click} order={order} data={JSON.stringify(data)}>
                {data ? data.template_name : null}
            </div>
        </>
    )
}





function DeliveryTemplate() {
    const [overlay, setOverlay] = useState("none");
    const [deleteModal, setDeleteModal] = useState(false);


    const [deliveryType, setDeliveryType] = useState("택배");
    const [deliveryPay, setDeliveryPay] = useState("무료");
    const [installPay, setInstallPay] = useState("있음");
    const [payType, setPayType] = useState("착불");
    const [template, setTemplate] = useState([]);
    const [selectedTemplate, selectTemplate] = useState();
    const [selectedIdx, selectIdx] = useState(null);

    const [templateName, setTemplateName] = useState();
    const [dvType, setDvType] = useState('택배');
    const [dvCost, setDvCost] = useState({type:"무료", cost: 0});
    const [dvInstallCost, setDvInstallCost] = useState("있음");
    const [dvPlace, setDvPlace] = useState();

    const [type2Cost, setType2Cost] = useState();
    const [type3Cost, setType3Cost] = useState();
    const [type4Cost, setType4Cost] = useState();
    const [dvReq, setDvReq] = useState();
    const [dvReq2, setDvReq2] = useState();

    const history = useHistory();

    useEffect(() => {
        if(!localStorage.getItem("token")) {
            alert('로그인 후 이용해주세요.');
            history.push("/");
        }

        const fetchData = async () => {
            const result = await fetch("/api/template/delivery/all", {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());
    
            if(result.success) {
                const tempArray = [];
                for(let i=0; i<result.data.length; i++) {
                    tempArray.push(
                        <TemplateChild click={onClickTemplate} data={result.data[i]} order={i}/>
                    )
            }
                
    
                setTemplate(tempArray);
            }else {
                if(result.code === -1) {
                    alert(result.msg);
                    history.push("/payment");
                }
            }
        }

        fetchData();

    }, []);


    const selectedButtonStyle = {
        display: "flex",
        alignItems: "center",
        justifyContents: "center",
        padding: "5px 10px",
        width: "120px",
        height: "25px",
        border: "1px solid #1c5ae1",
        backgroundColor: "#1c5ae1",
        color: "white"
    };

    const openDeleteModal = () => {
        setDeleteModal(true);
        setOverlay("block");
    }
    
    const closeDeleteModal = () => {
        setDeleteModal(false);
        setOverlay("none");
    }
    
    const removeProduct =  async () => {
        if(!selectedIdx) {
            alert('삭제할 템플릿을 선택해주세요.');
            return;
        }

        const result = await fetch("/api/template/delivery?_method=DELETE", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({selectedIdx})
        }).then(res => res.json());

        if(result.success) {
            alert('삭제 되었습니다.');
            closeDeleteModal();
            window.location.reload();
        }
    }


    const onClickDeliveryType = (e) => {
        const value = e.currentTarget.getAttribute("value");
        setDeliveryType(value);
        setDvType(value);
    }

    const onClickDeliveryPay = (e) => {
        const value = e.currentTarget.getAttribute("value");
        setDeliveryPay(value);
    }

    const onClickInstallPay = (e) => {
        const value = e.currentTarget.getAttribute("value");
        setInstallPay(value);
        setDvInstallCost(value);

    }


    const onClickTemplate = (e) => {
        
        selectTemplate(e.currentTarget);
        const customJson = JSON.parse(e.currentTarget.getAttribute("data"));
        const dvCost = JSON.parse(customJson.dv_cost);
        setDeliveryType(customJson.dv_type);
        setInstallPay(customJson.dv_install_cost);
        setDeliveryPay(dvCost.type);
        setTemplateName(customJson.template_name);
        setDvPlace(customJson.dv_place);

        selectIdx(customJson.template_idx);
        if(dvCost.type === "무료") {
            
        }else if(dvCost.type === "조건부 무료") {
            setType2Cost(dvCost.cost);
            setDvReq(dvCost.dvReq);
            setPayType(dvCost.payType);
        }else if(dvCost.type === "유료") {
            setType3Cost(dvCost.cost);
            setPayType(dvCost.payType);
        }else if(dvCost.type === "수량별") {
            setType4Cost(dvCost.cost);
            setDvReq2(dvCost.dvReq);
            setPayType(dvCost.payType);
        }
    }

    const onClickPayType = (e) => {
        const value = e.currentTarget.getAttribute("value");
        setPayType(value);
    }

    const onCreateDelivery = async () => {

        let customJson = {
            templateName: templateName,
            dvType: dvType,
            dvInstallCost: dvInstallCost,
            dvPlace: dvPlace
        };

        if(deliveryPay === "무료") {
            customJson = {...customJson, dvCost: dvCost}
        }
        else if(deliveryPay === "조건부 무료") {
            customJson = {...customJson, dvCost: {type: '조건부 무료', cost: type2Cost, dvReq: dvReq, payType}}
        }else if(deliveryPay === "유료") {
            customJson = {...customJson, dvCost: {type: '유료', cost: type3Cost, payType}}
        }else if(deliveryPay === "수량별") {
            customJson = {...customJson, dvCost: {type: '수량별', cost: type4Cost, dvReq: dvReq2, payType}}
        }

        console.log(customJson);

        const result = await fetch("/api/template/delivery", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({requestJson: customJson, selectedIdx})
        }).then(res => res.json());

        if(result.success) {
            alert('템플릿이 등록되었습니다.');
            window.location.reload();
        }else {
            alert('오류가 발생하였습니다.');
        }
    }

    const onChangeTemplateName = (e) => {
        setTemplateName(e.target.value);
    }

    const onChangeDvPlace = (e) => {
        setDvPlace(e.target.value);
    }

    const onType2CostChange = (e) => {
        setType2Cost(e.target.value);
    }
    const onType3CostChange = (e) => {
        setType3Cost(e.target.value);
    }

    const onChangeDvReq = (e) => {
        setDvReq(e.target.value);
    }

    const onType4CostChange = (e) => {
        setType4Cost(e.target.value);
    }

    const onChangeDvReq2 = (e) => {
        setDvReq2(e.target.value);
    }

    const onClickAddTemplate = () => {
        window.location.reload();
    }


    return(
        <div className="template__wrapper">
            <div className="overlay" style={{display: overlay}}></div>
            <h1 className="template__title">배송 템플릿 목록</h1>
            <div className="template__boxes">
                <div className="template__lists">
                    {template}


                    <div className="template__lists--addBtn" onClick={onClickAddTemplate}>템플릿 추가</div>
                </div>

                <div className="template__contents">

                        <div className="template__contents__row">
                            <div className="template__contents__row--title">이름</div>
                            <input type="text" className="template__contents__row--input" value={templateName} onChange={onChangeTemplateName}/>
                        </div>

                        <div className="template__contents__row">
                            <div className="template__contents__row--title">배송 방법</div>
                            <div className={deliveryType === "택배" ? "template__toggle--btn--selected": "template__toggle--btn"} value="택배" onClick={onClickDeliveryType}>택배</div>
                            <div className={deliveryType === "직접" ? "template__toggle--btn--selected": "template__toggle--btn"} value="직접" onClick={onClickDeliveryType}>직접 (화물배달)</div>
                        </div>

                        <div className="template__contents__row">
                            <div className="template__contents__row--title">배송비</div>
                            <div className={deliveryPay === "무료" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickDeliveryPay} value="무료">무료</div>
                            <div className={deliveryPay === "조건부 무료" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickDeliveryPay} value="조건부 무료">조건부 무료</div>
                            <div className={deliveryPay === "유료" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickDeliveryPay} value="유료">유료</div>
                            <div className={deliveryPay === "수량별" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickDeliveryPay} value="수량별">수량별</div>
                        </div>   

                        {deliveryPay === "조건부 무료" ? 
                            <div className="template__contents__row">
                                
                                    <div className="template__contents__row--hidden">
                                        <div className="template__contents__row__inside">
                                            <div className="template__contents__row--title">기본 배송비</div>
                                            <input type="text" className="template__contents__row--input" value={type2Cost} onChange={onType2CostChange}/>
                                            <p className="template__contents__row--endText">원</p>
                                        </div>

                                        <div className="template__contents__row__inside">
                                            <div className="template__contents__row--title">배송비 조건</div>
                                            <input type="text" className="template__contents__row--input" value={dvReq} onChange={onChangeDvReq}/>
                                            <p className="template__contents__row--endText">원 이상</p>
                                        </div>       

                                        <div className="template__contents__row__inside">
                                            <div className="template__contents__row--title">결제 방식</div>
                                            <div className={payType === "착불" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickPayType} value="착불">착불</div>
                                            <div className={payType === "선결제" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickPayType} value="선결제">선결제</div>
                                        </div>                                                            
                                    </div>

                                
                            </div>
                        : null}

                        {deliveryPay === "유료" ? 
                            <div className="template__contents__row">
                                
                                    <div className="template__contents__row--hidden">
                                        <div className="template__contents__row__inside">
                                            <div className="template__contents__row--title">기본 배송비</div>
                                            <input type="text" className="template__contents__row--input" value={type3Cost} onChange={onType3CostChange}/>
                                            <p className="template__contents__row--endText">원</p>
                                        </div>

                                        <div className="template__contents__row__inside">
                                            <div className="template__contents__row--title">결제 방식</div>
                                            <div className={payType === "착불" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickPayType} value="착불">착불</div>
                                            <div className={payType === "선결제" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickPayType} value="선결제">선결제</div>
                                        </div>                                                            
                                    </div>

                                
                            </div>
                        : null}             

                        {deliveryPay === "수량별" ? 
                            <div className="template__contents__row">
                                
                                    <div className="template__contents__row--hidden">
                                        <div className="template__contents__row__inside">
                                            <div className="template__contents__row--title">기본 배송비</div>
                                            <input type="text" className="template__contents__row--input" value={type4Cost} onChange={onType4CostChange}/>
                                            <p className="template__contents__row--endText">원</p>
                                        </div>

                                        <div className="template__contents__row__inside">
                                            <div className="template__contents__row--title">배송비 조건</div>
                                            <input type="text" className="template__contents__row--input" value={dvReq2} onChange={onChangeDvReq2} />
                                            <p className="template__contents__row--endText">개마다 배송비 반복 부가</p>
                                        </div>   

                                        <div className="template__contents__row__inside">
                                            <div className="template__contents__row--title">결제 방식</div>
                                            <div className={payType === "착불" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickPayType} value="착불">착불</div>
                                            <div className={payType === "선결제" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickPayType} value="선결제">선결제</div>
                                        </div>                                                            
                                    </div>

                                
                            </div>
                        : null}                                    

                        <div className="template__contents__row">
                            <div className="template__contents__row--title">별도 설치비</div>
                            <div className={installPay === "있음" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickInstallPay} value="있음">있음</div>
                            <div className={installPay === "없음" ? "template__toggle--btn--selected" : "template__toggle--btn"} onClick={onClickInstallPay} value="없음">없음</div>
                        </div>     

                        <div className="template__contents__row">
                            <div className="template__contents__row--title">출고지</div>
                            <input type="text" className="template__contents__row--input" value={dvPlace} onChange={onChangeDvPlace}/>
                        </div>              
                </div>
                
            </div>

            <div className="template__action__box">
                    <div className="template__deleteBtn" onClick={openDeleteModal}>
                        삭제
                    </div>
                    <div className="template__createBtn" onClick={onCreateDelivery}>
                        등록/수정
                    </div>
            </div>
            <DeletePopup isOpen={deleteModal} close={closeDeleteModal} remove={removeProduct}></DeletePopup>
        </div>
    )
}

export default DeliveryTemplate;