import React, { useState, useEffect } from "react";
import {Link, useHistory} from "react-router-dom";
import classnames from "classnames";



function Header(props) {
    const [login ,setLogin] = useState(false);
    const history = useHistory();
    useEffect(() => {
        if(localStorage.getItem("token"))
            setLogin(true);
        else 
            setLogin(false);
    }, []);

    const logout = () => {

        if(window.confirm("로그아웃 하시겠습니까?")) {
            localStorage.removeItem("token");
            history.push("/");
        }
        

    }
    return(
        <>
            <div className="manage__header">
                <div className="manage__header__column">
                    <Link to="/manage">
                        <h3 className="manage__header__title">Item Booster</h3>
                    </Link>
                </div>


                <div className="manage__header__column">
                    {props.type === "productList" ?
                        <>
                            <button className={classnames("manage__header__default--btn", "manage__header__download--btn")}>DB 다운로드</button>
                            <button className={classnames("manage__header__default--btn", "manage__header__delete--btn")} onClick={props.openDeleteModal}>삭제</button>
                        </>
                    : null}

                    {props.type === "boardList" && login ?
                        <>
                            <Link to="/board/write">
                                <button className={classnames("manage__header__default--btn", "manage__header__download--btn")} style={{marginRight: '5px'}}>글쓰기</button>
                            </Link>
                        </>
                
                    :null}
                    {login ? 
                        <button className={classnames("manage__header__default--btn", "manage__header__logout--btn")} onClick={logout}>로그아웃</button>
                    : null}
                    
                </div>
            </div>
        </>
    )
}

export default Header;