import React, {useState} from "react";
import classnames from "classnames";

function AdminBoardTable(props) {
    const {boardData} = props;
    const items = [];

    if(boardData) {
        for(const [i, data] of boardData.entries()) {
            items.push(
                <tr className="boardTable__body" key={i}>
                    <td>{i+1}</td>
                        <td>
                            {data.imp_uid}
                        </td>
                    <td>{data.name}</td>
                    <td>{data.amount}</td>
                    <td>{data.payment_date}</td>
                </tr>
            )
        }
    }
    return(
        <div className="boardTable">
            <table className="board__wrapper">
                <thead>
                    <tr className="boardTable__head">
                        <th>번호</th>
                        <th>UUID</th>
                        <th>결제자</th>
                        <th>결제금액</th>
                        <th>결제일</th>
                    </tr>
                </thead>

                <tbody>
                    {items}                                                                                                                                                                                
                </tbody>
            </table>
        </div>
    )
}

export default AdminBoardTable;