import React, { useState } from "react";
import classnames from "classnames";

function DeletePopup (props) {
    const { isOpen, close, remove } = props;
    return(
        <>
        {isOpen ? (
            <div className="modal" style={{height: "160px"}}>
                <p className="modal__title"  style={{marginBottom: "10px"}}>삭제하시겠습니까?</p>
                <div className="modal__buttonBox">
                    <button className="modal__login--cancel" onClick={close}>취소</button>
                    <button className="modal__remove--btn" onClick={remove}>삭제</button>
                </div>
            </div>
        ): null}
        </>
    )
}

export default DeletePopup;