import React, { useState } from "react";
import classnames from "classnames";


function uploadPopup (props) {
    const { isOpen, close, remove, thumbnail, changeInputPopup, getLink, click} = props;
    return(
        <>
        {isOpen ? (
            <div className="modal" style={{width: "508px", height: "500px"}}>
                <p className="modal__title"  style={{marginBottom: "20px"}}>이미지 등록</p>
                <div className="imageUploadPopup__image" style={{backgroundImage: thumbnail}}></div>

                <div className="modal__linkbox">
                    <div className="modal__link--text">링크 주소 불러오기</div>
                    <input type="text" className="modal__link--input" onChange={changeInputPopup}/>
                    <button className="browse--btn" onClick={getLink}>불러오기</button>
                </div>

                <div className="modal__buttonBox">
                    <button className="modal__login--cancel" onClick={close}>취소</button>
                    <button className="modal__remove--btn" onClick={click}>등록</button>
                </div>
            </div>
        ): null}
        </>
    )
}

export default uploadPopup;