import React, { useState } from "react";
import {Link} from "react-router-dom";
import classnames from "classnames";
import '../../resources/css/global.css';

import {faUsersCog, faTasks, faCog, faClipboard} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
function Sidebar(props) {
    const {sidebarList , selectedItem, sidebarLinks} = props;
    return(
        <>
            <div className="sidebar">
                <Link to={sidebarLinks[0]}>
                    <div className="sidebar__column">
                        <FontAwesomeIcon icon={faUsersCog} className={classnames('manageMainCog', 'sidebar__icon', selectedItem === "manage" ? "sidebar__title--selected": "sidebar__title")}/>
                        <p className={classnames("sidebar__title", selectedItem === "manage" ? "sidebar__icon--selected": "sidebar__icon")}>{sidebarList[0]}</p>
                    </div>
                </Link>

                <Link to={sidebarLinks[1]}>
                    <div className="sidebar__column">
                        <FontAwesomeIcon icon={faTasks} className={classnames('manageTasks', 'sidebar__icon', selectedItem === "productList" ? "sidebar__title--selected" : "sidebar__title")}/>
                        <p className={classnames("sidebar__title", selectedItem === "productList" ? "sidebar__icon--selected": "sidebar__icon")}>{sidebarList[1]}</p>
                    </div>
                </Link>

                <Link to={sidebarLinks[2]}>
                    <div className="sidebar__column">
                        <FontAwesomeIcon icon={faCog} className={classnames('manageCog', 'sidebar__icon', selectedItem === "templates" ? "sidebar__title--selected" : "sidebar__title")}/>
                        <p className={classnames("sidebar__title", selectedItem === "templates" ? "sidebar__icon--selected": "sidebar__icon")}>{sidebarList[2]}</p>
                    </div>
                </Link>

                <Link to={sidebarLinks[3]}>
                    <div className="sidebar__column">
                        <FontAwesomeIcon icon={faClipboard} className={classnames('manageClip', 'sidebar__icon', selectedItem === "boards" ? "sidebar__title--selected" : "sidebar__title")}/>
                        <p className={classnames("sidebar__title", selectedItem === "boards" ? "sidebar__icon--selected": "sidebar__icon")}>{sidebarList[3]}</p>
                    </div>
                </Link>
            </div>
        </>
    )
}

export default Sidebar;