import React, {useState, useEffect, useRef} from "react";
import { useHistory } from "react-router";
import DefaultImage from "../../resources/images/image_add.png";
import DeletePopup from "../utils/deletePopup";

function TemplateChild (props)  {
    const {click, order, data, key} = props;
    return(
        <>
            <div className="template__lists--list" onClick={click} order={order} data={JSON.stringify(data)} >
            {data ? data.template_name : null}
            </div>
        </>
    )
}

function ImagesTemplate() {
    const [overlay, setOverlay] = useState("none");
    const [deleteModal, setDeleteModal] = useState(false);


    const [imageType, setImageType] = useState("헤더");
    const [template, setTemplate] = useState([]);
    const [selecetedTemplate, selectTemplate] = useState();
    const [thumbnail, setThumbnail] = useState(`url(${DefaultImage})`);
    const [thumbnailURL, setThumbnailURL] = useState('');
    const [thumbnailFile, setThumbnailFile] = useState();

    const thumbnailRef = useRef();

    const history = useHistory();
    const [selectedIdx, selectIdx] = useState();

    const [templateName, setTemplateName] = useState();
    const [image, setImage] = useState();
    const [selectedImage, setSelectImage] = useState();

    useEffect(() => {
        if(!localStorage.getItem("token")) {
            alert('로그인 후 이용해주세요.');
            history.push("/");
        }

        
        const fetchData = async () => {
            const result = await fetch("/api/template/image/all", {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());
    

            if(result.success) {
                const tempArray = [];
                for(let i=0; i<result.data.length; i++) {
                    tempArray.push(
                        <TemplateChild click={onClickTemplate} data={result.data[i]} order={i} key={i}/>
                    )
                }
                
    
                setTemplate(tempArray);
            }else {
                if(result.code === -1) {
                    alert(result.msg);
                    history.push("/payment");
                }
            }

        }

        fetchData();        
    }, []);

    const onCreateImages = async () => {

        const formData = new FormData();

        formData.append("templateName", templateName);
        formData.append("imageType", imageType);
        formData.append("selectedIdx", selectedIdx);
        if(image) {
            formData.append("image", image);
        }else {
            formData.append("imageURL", selectedImage);
        }

        const result = await fetch("/api/template/image", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`
            },
            method: "POST",
            body: formData
        }).then(res => res.json());

        if(result.success) {
            alert('템플릿이 등록되었습니다.');
            window.location.reload();
        }else {
            alert('오류가 발생하였습니다.');
        }
    }

    const onChangeTemplateName = (e) => {
        setTemplateName(e.target.value);
    }

    const onClickTemplate = (e) => {
        selectTemplate(e.currentTarget);
        const customJson = JSON.parse(e.currentTarget.getAttribute("data"));
        
        selectIdx(customJson.template_idx);
        setTemplateName(customJson.template_name);
        setImageType(customJson.image_type);
        setThumbnail(`url(${customJson.image_url})`);
        setSelectImage(customJson.image_url);
        /* TODO Change color */
    }

    const onClickAddTemplate = () => {
        window.location.reload();
    }

    const onClickImageType = (e) => {
        const value = e.currentTarget.getAttribute("value");
        setImageType(value);
    }


    const openLocalImage = () => {
        thumbnailRef.current.click();
    }

    const onThumbnailFileChange = (e) => {
        const file = e.dataTransfer ? e.dataTransfer.files[0] : e.currentTarget.files[0];
        setThumbnail(`url(${URL.createObjectURL(file)})`);
        setImage(e.target.files[0]);
    }

    const openDeleteModal = () => {
        setDeleteModal(true);
        setOverlay("block");
    }
    
    const closeDeleteModal = () => {
        setDeleteModal(false);
        setOverlay("none");
    }
    
    const removeProduct = async () => {
        if(!selectedIdx) {
            alert('삭제할 템플릿을 선택해주세요.');
            return;
        }

        const result = await fetch("/api/template/image?_method=DELETE", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({selectedIdx})
        }).then(res => res.json());

        if(result.success) {
            alert('삭제 되었습니다.');
            closeDeleteModal();
            window.location.reload();
        }
    }


    return(
        <div className="template__wrapper">
            <div className="overlay" style={{display: overlay}}></div>
            <h1 className="template__title">상세 / 상하단 이미지 템플릿 목록</h1>
            <div className="template__boxes">
                <div className="template__lists">
                    {template}


                    <div className="template__lists--addBtn" onClick={onClickAddTemplate}>템플릿 추가</div>
                </div>

                <div className="template__contents">
                    <div className="template__contents__row">
                        <div className="template__contents__row--title">이름</div>
                        <input type="text" className="template__contents__row--input" value={templateName} onChange={onChangeTemplateName}/>
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">종류</div>
                        <div className={imageType === "헤더" ? "template__toggle--btn--selected": "template__toggle--btn"} value="헤더" onClick={onClickImageType}>헤더</div>
                        <div className={imageType === "푸터" ? "template__toggle--btn--selected": "template__toggle--btn"} value="푸터" onClick={onClickImageType}>푸터</div>
                    </div>

                    <div className="template__contents__row">
                        <div className="template__contents__row--title">이미지</div>
                        <div className="template__contents__update--img" style={{backgroundImage: thumbnail}} onClick={openLocalImage}></div>
                    </div>                                                                          
                </div>
            </div>

            <div className="template__action__box">
                    <div className="template__deleteBtn" onClick={openDeleteModal}>
                        삭제
                    </div>
                    <div className="template__createBtn" onClick={onCreateImages}> 
                        등록/수정
                    </div>
                    <input type="file" hidden value={thumbnailFile} ref={thumbnailRef} onChange={onThumbnailFileChange}/>
            </div>
            <DeletePopup isOpen={deleteModal} close={closeDeleteModal} remove={removeProduct}></DeletePopup>
        </div>
    )
}

export default ImagesTemplate;