import React, {useState} from "react";
import classnames from "classnames";
import leftArrowIcon from "../../resources/images/left_arrow.png";
import rightArrowIcon from "../../resources/images/right_arrow.png";

function Pagination(props) {
    const {pdTable, totalPosts, paginate, postsPerPage, currentPage} = props;
    const pageNumbers = [];

    console.log(currentPage);
    for(let i=0; i<Math.ceil(totalPosts / postsPerPage); i++) {
        pageNumbers.push(i);
    }

    return(
        <div className="lists_pagination" style={{paddingRight: pdTable}}>

            {/* <img src={leftArrowIcon} className={classnames('lists__previousButton', 'pagination__assets')}/>             */}
            {pageNumbers.map(numbers => (
                <div className={classnames("pageButton", (numbers+1) === currentPage ? "pageButton--selected" : null)} onClick={() => paginate(numbers+1)}>{numbers+1}</div>
            ))}
            {/* <img src={rightArrowIcon} className={classnames('lists__nextButton', 'pagination__assets')}/> */}
        </div>
    )
}

export default Pagination;