import React, { useState, useEffect, useRef, createRef } from "react";
import classnames from "classnames";
import { useHistory } from "react-router";
import FacebookLogin from "react-facebook-login";


function LoginPopup (props) {

    const { isOpen, close } = props;
    const { Kakao } = window;    
    const history = useHistory();

    function kakaoLogin() {
        Kakao.Auth.login({
            success: (response) => {
                fetch("/api/auth/kakao", {
                    headers: {
                        "Content-Type": "application/json",
                        Authorization: response.access_token
                    }
                }).then(res => res.json()).then(result => {
                    if(result.success) {
                        if(result.new) {
                            alert('자동 회원가입이 되셨습니다.');
                        }else {
                            alert('로그인에 성공하였습니다.');
                        }

                        console.log(result.type);

                        localStorage.setItem("token", result.token);

                        history.push("/manage");
                    }
                })
            },
            fail: (error) => {
              alert(JSON.stringify(error));
            },
        });
    }

   
    const naverLogin = () => {
        const win = window.open("https://nid.naver.com/oauth2.0/authorize?response_type=token&client_id=7kUS_xAurM2s0LPb_yo7&state=23de022b-4b9b-46dc-abee-8a4979c40148&redirect_uri=http://localhost:3000/auth/naver&version=js-2.0.0", "_parent");
    }

    const facebookLogin = (userInfo) => {
        console.log(userInfo);
        fetch("/api/auth/facebook", {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify(userInfo)
        }).then(res => res.json()).then(result => {
            if(result.success) {
                if(result.new) {
                    alert('자동 회원가입이 되셨습니다.');
                }else {
                    alert('로그인에 성공하였습니다.');
                }

                localStorage.setItem("token", result.token);

                history.push("/manage");
            }
        })

    }

    return(
        
        <>

        {isOpen ? (
            <div className="modal">
                <p className="modal__title">SNS 로그인</p>
                <div className="modal__groups">
                

                
                <div className={classnames("modal__button--naver", "modal__login--btn")} onClick={naverLogin}>
                    <span className="modal__button--text">네이버로 로그인</span>
                </div>

                
                <FacebookLogin
                    appId="205481591195273"
                    autoLoad={false}
                    fields="name,email"
                    callback={facebookLogin}
                    onFailure={response => alert(JSON.stringify(response))}
                    cssClass="modal__button--facebook modal__login--btn modal__button--text"
                    textButton='페이스북으로 로그인'
                />


                <div className={classnames("modal__button--kakao", "modal__login--btn")} onClick={kakaoLogin}>
                    <span className="modal__button--text">카카오로 로그인</span>
                </div>
                </div>
                <button className="modal__login--cancel" onClick={close}>취소</button>
            </div>
        ): null}
        </>
    )
}

export default LoginPopup;