import React, { useState, useEffect, useRef } from "react";
import classnames from "classnames";
import '../../resources/css/global.css';

import '../../resources/css/header.css';
import '../../resources/css/sidebar.css';
import '../../resources/css/App.css';
import "../../resources/css/manage.css";
import "../../resources/css/payment.css";
import "../../resources/css/productList.css";
import "../../resources/css/productCreate.css";
import "../../resources/css/board.css";
import "../../resources/css/admin.css";
import "../../resources/css/ads.css";
import "../../resources/css/template.css";
import "../../resources/css/editor.css";
import "../../resources/css/boardView.css";
import "../../resources/css/comments.css";
import defaultImage1 from "../../resources/images/main_default1.jpg";
import defaultImage2 from "../../resources/images/main_default2.jpg";
import defaultImage3 from "../../resources/images/main_default3.jpg";
import defaultImage4 from "../../resources/images/main_default4.jpg";

import LoginPopup from "./LoginPopup";
import { useHistory } from "react-router";

function App() {
  const [modal, setModal] = useState(false);
  const [overlay, setOverlay] = useState("none");
  const [login, setLogin] = useState(false);
  const [images, setImages] = useState();
  const history = useHistory();

  useEffect(async () => {
    if(localStorage.getItem("token")) 
      setLogin(true);
    else 
      setLogin(false);

    const images = await fetch("/api/ads", {
      method: "GET"
    }).then(res => res.json());

    setImages(images.images);
  }, []);

  const openModal = () => {
    setModal(true);
    setOverlay("block");



  }

  const closeModal = () => {
    setModal(false);
    setOverlay("none");
  }
  
  return (
    <div className="container">
      <div className="overlay" style={{display: overlay}}></div>
      <div className="header">
        {login ?
          <button className="header__login--btn" onClick={() => history.push("/manage")}>관리 페이지</button>
        : 
          <button className="header__login--btn" onClick={openModal}>SNS 로그인</button>
        }
        
        
      </div>
      
      <div className="main">
        <h1 className="main__title">Item Booster</h1>
        <div className="main__images__box">
          <img src={images  && images[0] ? images[0].image_url: null} className="main__images--img"/>
          <img src={images && images[1] ? images[1].image_url: null} className="main__images--img"/>
        </div>

        <div className="main__images__box">
          <img src={images && images[2] ? images[2].image_url: null} className="main__images--img"/>
          <img src={images && images[3] ? images[3].image_url: null} className="main__images--img"/>
        </div>

      </div>
      <LoginPopup isOpen={modal} close={closeModal}>
      </LoginPopup>
      
    </div>
  );
}

export default App;
