import React, { useState } from "react";
import {Link} from "react-router-dom";
import classnames from "classnames";
function BoardNav(props) {
    const {selectedNav, children, navList, navURL, admin, single} = props;

    const selectedStyles = {
        color: "#001d36",
        borderBottom: "3px solid #001d36"
    };


    return(
        <div className="boardNavWrapper">
            <div className="boardNav">
                {admin || single ?
                    <>
                        <Link to={navURL[0]}>
                            <div className="boardNav__box">
                                <h2 className={classnames("nav__item--default")} style={selectedNav === navList[0] ? selectedStyles: null}>{navList[0]}</h2>
                            </div>
                        </Link>
                    </>
                : 
                    <>
                        <Link to={navURL[0]}>
                            <div className="boardNav__box">
                                <h2 className={classnames("nav__item--default")} style={selectedNav === navList[0] ? selectedStyles: null}>{navList[0]}</h2>
                            </div>
                        </Link>

                        <Link to={navURL[1]}>
                            <div className="boardNav__box">
                                <h2 className={classnames("nav__item--default")} style={selectedNav === navList[1] ? selectedStyles: null}>{navList[1]}</h2>
                            </div>
                        </Link>

                        <Link to={navURL[2]}>
                            <div className="boardNav__box">
                                <h2 className={classnames("nav__item--default")} style={selectedNav === navList[2] ? selectedStyles: null}>{navList[2]}</h2>
                            </div>
                        </Link>

                        <Link to={navURL[3]}>
                            <div className="boardNav__box">
                                <h2 className={classnames("nav__item--default")} style={selectedNav === navList[3] ? selectedStyles: null}>{navList[3]}</h2>
                            </div>
                        </Link>
                    </>
                }
            </div>
            {children}
        </div>
    )
}

export default BoardNav;
