import React, { useState, useEffect } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardNav from "../partials/BoardNav";
import profile from "../../resources/images/sample_profile1.png";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";
function BoardView(props) {
    const history = useHistory();
    const [post, setPost] = useState();
    const [category, setCategory] = useState('');
    const [comment, setComment] = useState();
    const params = new URLSearchParams(props.location.search);
    const board_type = Number(params.get("board_type"));
    const post_idx = Number(params.get("post_idx"));
    const [userType, setUserType] = useState();
    const [comments, setComments] = useState();

    const commentsDOM =  [];

    useEffect(async () => {
        if(!board_type || !post_idx) {
            alert("잘못된 접근 입니다.");
            history.goBack();
        }

        if(!localStorage.getItem("token")) {
            alert('로그인 후 열람 가능합니다.');
            history.push("/");
        }
        
        const result = await fetch(`/api/board/view?board_type=${board_type}&post_idx=${post_idx}`, {
            method: "GET",
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`
            }
        }).then(response => response.json());

        if(result.success) {
        
            if(result.data[0].board_type === 1) setCategory('공지사항');
            else if(result.data[0].board_type === 2) setCategory('문의/답변');
            else if(result.data[0].board_type === 3) setCategory('시스템 오류');
            else if(result.data[0].board_type ===4) setCategory('시스템 개선 요청');
    
            setPost(result.data[0]);
            setUserType(result.type);
        }else {
            alert('본인만 열람 가능합니다.');
            history.goBack();
        }

        const commentList = await fetch(`/api/comment?board_type=${board_type}&post_idx=${post_idx}`, {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`
            },
            method: "GET"
        }).then(res => res.json());

        console.log(commentList);
        setComments(commentList.data);

    }, []);

    
    if(comments) {

        for(let i=0; i<comments.length; i++) {
            console.log(i);
            commentsDOM.push(
                <div className="board__comments">
                    <p className="board__comments--name">{comments[i].author}</p>
                    <p className="board__comments--content">{comments[i].contents}</p>
                    <p className="board__comments--date">{comments[i].created_at.split("T")[0]} 작성</p>
                </div>
            )
        }
    }


  


    const onBackButtonClick = () => {
        history.goBack();
    }

    const onDeleteButtonClick = async () => {
        if(window.confirm("삭제하시겠습니까?")) {
            const result = await fetch("/api/board?_method=DELETE", {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`,
                    "Content-Type": "application/json"
                },
                method: "POST",
                body: JSON.stringify({post_idx, board_type})
            }).then(res => res.json());


            if(result.success) {
                alert('삭제되었습니다.');
                history.goBack();
            }else {
                alert('오류가 발생하였습니다.');
            }
        }
    }

    const onChangeInput = (e) => {
        setComment(e.target.value);
        console.log(comment);
    }

    const onClickWriteComment = async () => {
        if(!comment) {
            alert('내용을 입력해주세요');
            return;
        }

        const result = await fetch("/api/comment", {
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({board_type, post_idx, comment})
        }).then(res => res.json());

        if(result.success) {
            alert('댓글이 등록되었습니다.');
            window.location.reload();
        }else {
            alert('오류가 발생하였습니다.');
        }
    }



    const navList = ["글 조회"];
    const navURL = ["#"];
    const sidebarList = ["관리페이지", "상품 리스트", "템플릿 관리", "게시판"];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];
    return (
        <div className="container">
            <Header type="boardList"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="boards" sidebarLinks={sidebarLinks}></Sidebar>
                <BoardNav selectedNav="글 조회" navList={navList} navURL={navURL} single="true">
                    <div className="board__view__wrapper">
                        <p className="board__category--text">
                            {category}
                        </p>
                        <h1 className="board__view--title">{post ? post.title : null}</h1>
                        <div className="board__view__header--wrap">
                            <img src={profile} style={{width: "30px", height: "30px"}} />
                            <div className="board__view__header--content">
                                <p className="board__view--author">관리자</p>
                                <p className="board__view--date">{post ? post.published_date.split("T")[0] : null}</p>
                            </div>
                            <div className="board__view__header--content--end">
                                <p className="board__view--author">조회수 {post ? post.views_count+1 : 0}</p>
                            </div>
                        </div>

                        <div className="board__view--contents" dangerouslySetInnerHTML={{__html: post ? post.contents : null}}>

                        </div>
                    </div>
                    <div className="editor__footer" style={{textAlign: "end", paddingRight: "5px", paddingBottom: "40px"}}>
                        <button className="lists__processButton lists__createButton" onClick={onBackButtonClick}>목록</button>
                        {board_type !== 1 ?
                            <>
                                <Link to={"/board/edit?board_type=" + board_type + "&post_idx=" + post_idx}>
                                    <button className="lists__processButton lists__createButton" >수정</button>
                                </Link>
                                
                                <button className="lists__processButton lists__createButton" onClick={onDeleteButtonClick}>삭제</button>
                            </>
                        : null}
                        {userType === 1 ?
                            <>
                                <Link to={"/board/edit?board_type=" + board_type + "&post_idx=" + post_idx}>
                                    <button className="lists__processButton lists__createButton" >수정</button>
                                </Link>
                                <button className="lists__processButton lists__createButton" onClick={onDeleteButtonClick}>삭제</button>
                            </>
                        : null}
                        
                    </div>

                    <div className="board__comment__wrapper">
                        {commentsDOM}                                             
                    </div>

                    <div className="board__comment--write">
                        <input type="text" className="board__comment--input" placeholder="댓글을 입력하세요" value={comment} onChange={onChangeInput}/>
                        <button className="board__comment--btn" onClick={onClickWriteComment}>등록</button>
                    </div>
                </BoardNav>
            </div>
        </div>
    )
}

export default BoardView;